<script>
export default {
  name: 'AdvanceTableActions',
  props: {
    actions: {
      type: Array,
      default: () => {
        return []
      }
    },
    isDesign: {
      type: Boolean
    }
  },
  computed: {
    selectedRow () {
      return this.rows.filter((item, index) => {
        const b = this.selected.some((select, key) => {
          return select === index
        })
        if (b) {
          return item
        }
      })
    }
  },
  render (h) {
    const buttons = this.actions.map(item => {
      const { label, action, powerCode, type, size, icon } = item
      return h(
        'el-button',
        {
          props: {
            type,
            size,
            icon
          },
          directives: [{
            name: 'permission',
            value: this.isDesign ? null : powerCode
          }],
          on: {
            click: () => {
              if (action === 'Todo') {
                this.$emit('todo', { option: item })
              } else if (action === 'Link' && item.linkConf.url) {
                window.open(item.linkConf.url, item.linkConf.target ? '_blank' : '')
              } else {
                this.$emit('action', item)
              }
            }
          }
        },
        label
      )
    })
    return h('el-button-group', {}, buttons)
  }
}
</script>
