export default {
  options (h, conf, key) {
    const list = []
    conf.__slot__.options.forEach(item => {
      list.push(
        <el-carousel-item
          style={{
            backgroundImage: `url(${item.value})`,
            backgroundPosition: 'center bottom',
            backgroundSize: 'auto 100%',
            backgroundRepeat: 'no-repeat'
          }}
        >
        </el-carousel-item>
      )
    })
    return list
  }
}
