<script>
// 
import ruleTrigger from '../static/ruleTrigger'
// 
import render from '../render/index.js'
import { evalFn, getCurrentDate, readListRecursion } from '../utils/index.js'
import { isDisabled, operateClick } from '../utils/step.js'
import { advanceForm } from '@cseed/public-utils'
/**
 * el-form-item
 */
const layouts = {
  // el-col
  colFormItem (h, scheme) {
    const config = scheme.__config__
    const senior = scheme.__senior__ || {}
    const isRead = this.mode === 'read' // 
    if (isRead) {
      scheme.readonly = true
      scheme.disabled = true
    }
    let labelWidth = config.labelWidth ? `${config.labelWidth}px` : null
    if (config.showLabel === false) labelWidth = '0'
    if (config.tag === 'slot') return renderSlot.call(this, h, scheme) // 
    return (
      <el-col span={config.span}>
        <el-form-item
          label-width={labelWidth}
          prop={ !isRead ? scheme.__vModel__ : '' }
        >
          { config.showLabel
            ? <span slot="label">
              { this.$t(config.label) }
              { senior.isShowTip
                ? <el-tooltip class="tip" effect={senior.tipEffect} placement="top-start">
                  <span slot="content">
                    <span domPropsInnerHTML={senior.tipContent}></span>
                  </span>
                  <i class="el-icon-question" style="margin-left: 5px"></i>
                </el-tooltip>
                : null
              }
            </span>
            : null
          }
          {modeRenderFn.call(this, h, scheme)}
        </el-form-item>
      </el-col>
    )
  },
  // el-row
  rowFormItem (h, scheme) {
    const config = scheme.__config__
    const child = renderChildren.apply(this, arguments)
    // 
    if (config.tag === 'container') {
      const formData = this[this.formConf.formModel]
      const value = formData[config.relationKey] + '' // 
      const relationVal = config.relationVal + '' // 
      if (config.relationKey && (relationVal !== 'undefined' || relationVal !== 'null')) {
        if (value === relationVal) {
          return (
            <el-col span={config.span}>
              <el-row gutter={scheme.gutter}>
                {child}
              </el-row>
            </el-col>
          )
        }
      }
    } else {
      const listeners = buildListeners.call(this, scheme)
      return (
        <el-col span={config.span}>
          <el-row gutter={scheme.gutter}>
            <render headers={this.headers} conf={scheme} {...{ on: listeners }}>
              {child}
            </render>
          </el-row>
        </el-col>
      )
    }
  },
  // el-steps
  mutilFormPage (h, scheme) {
    const config = scheme.__config__
    const listeners = buildListeners.call(this, scheme)
    const stepContentList = renderContentStep.apply(this, arguments)
    return (
      <el-col span={ config.span }>
        <render headers={this.headers} conf={scheme} {...{ on: listeners }}></render>
        { stepContentList }
        { config.tag === 'el-steps' ? stepBtns.apply(this, arguments) : null }
      </el-col>
    )
  }
}

/**
 * 
 * edit add 
 * read  ()
 * readModeShowOrigin true:  false: 
 */
function modeRenderFn (h, scheme) {
  const config = scheme.__config__
  const slot = scheme.__slot__
  const senior = scheme.__senior__
  const { labelMap, valueMap } = scheme.dynamicConfig || {}
  const listeners = buildListeners.call(this, scheme)
  if (this.mode === 'read' && !senior.readModeShowOrigin) {
    const list = (slot && slot.options) || (scheme[config.dataMapping])
    const keyval = advanceForm.isTrusData(config.defaultValue)
      ? config.defaultValue
      : advanceForm.isTrusData(scheme.percentage)
        ? scheme.percentage
        : '--'
    if (list && list.length) {
      let temp = []
      // TODO  
      // el-select el-transfer el-cascader el-checkbox
      if (Array.isArray(keyval)) {
        temp = readListRecursion({ list, keyval, temp, labelMap, valueMap })
      } else {
        list.forEach(item => {
          if (item[valueMap] === keyval) {
            temp.push(item[labelMap])
          }
        })
      }
      return (<span>{temp.join(',')}</span>)
    } else if (config.tag === 'el-upload') {
      let imgList
      if (!keyval) {
        imgList = <el-col span={4}>
          <el-image class="readElUploadImage"></el-image>
        </el-col>
      } else {
        imgList = keyval && keyval.map(item => {
          return <el-col span={4}>
            <el-image class="readElUploadImage" src={item} fit="cover"></el-image>
          </el-col>
        })
      }
      return <el-row>
        {imgList}
      </el-row>
    } else if (config.tag === 'el-image' || config.tag === 'el-avatar') {
      return h(config.tag, { attrs: scheme, style: scheme.style })
    } else if (config.tag === 'el-switch') {
      let showText = keyval || '--'
      if (scheme['active-text'] || scheme['inactive-text']) {
        showText = keyval === scheme['active-value'] ? scheme['active-text'] : scheme['inactive-text']
      }
      return (<span domPropsInnerHTML={showText}></span>)
    } else {
      return (<span domPropsInnerHTML={keyval}></span>)
    }
  } else {
    return (
      <render headers={this.headers} conf={scheme} {...{ on: listeners }} />
    )
  }
}

/**
 * slot
 */
function renderSlot (h, scheme) {
  const config = scheme.__config__
  const slot = scheme.__slot__ || {}
  config.componentName = config.label
  const slotName = slot && slot.slotName ? slot.slotName : 'default'
  return (
    <el-col span={config.span} style="margin-left: 0">
      { this.$slots[slotName] }
    </el-col>
  )
}

/**
 * 
 */
function renderFormItem (h, elementList) {
  return elementList.map(scheme => {
    const config = scheme.__config__
    const layout = layouts[config.layout]
    if (layout) {
      return layout.call(this, h, scheme)
    }
    return false
  })
}

/**
 * 
 */
function renderContentStep (h, scheme) {
  const config = scheme.__config__
  const steps = scheme.__slot__.options
  const active = scheme.active
  return steps.map((step, i) => {
    if (config.tag === 'el-steps' && active === (i + 1)) {
      return renderFormItem.call(this, h, step.children)
    } else if (config.tag === 'el-tabs' && config.defaultValue === step.name) {
      return renderFormItem.call(this, h, step.children)
    }
  })
}

/**
 * 
 */
function stepBtns (h, scheme) {
  const config = scheme.__config__
  const btns = scheme.__slot__.btnGroup
  const active = scheme.active
  const child = btns.map(btn => {
    const disabled = isDisabled(scheme, btn, active)
    const customBtnShow = <el-button
      size="mini"
      type={ btn.type }
      disabled={ disabled }
      onClick={event => {
        operateClick.call(this, scheme, btn, this[this.formConf.formModel])
        event.stopPropagation()
      }}
    >
      {btn.label}
    </el-button>
    if (btn.operate === 'custom' && btn.stepShow) {
      if (scheme.active === btn.stepShow) {
        return customBtnShow
      }
    } else {
      return customBtnShow
    }
  })
  return (
    <div class="step-btn-group" style={{ textAlign: config.btnGroupAlign }}>
      { child }
    </div>
  )
}

/**
 * 
 */
function renderChildren (h, scheme) {
  const config = scheme.__config__
  if (!Array.isArray(config.children)) return null
  return renderFormItem.call(this, h, config.children)
}

/**
 * 
 */
function formBtns (h) {
  if (this.showBtns && this.formConf.showBtns && this.mode !== 'read') {
    const position = this.formConf.btnPosition || 'left'
    const name = this.$t(this.formConf.btnName) || this.$t('lower.save')
    const cancelName = this.$t('lower.cancel')
    const labelWidth = position === 'left' ? `${this.formConf.labelWidth}px` : 0
    // 
    // const btnList = this.formConf.viewBtnList.map(item => {
    //   const { label, type, icon, size, powerCode } = item
    //   return h(
    //     'el-button',
    //     {
    //       props: {
    //         type,
    //         size,
    //         icon
    //       },
    //       directives: [{
    //         name: 'permission',
    //         value: this.isDesign ? null : powerCode
    //       }],
    //       on: {
    //         click: () => {
    //           if (action === 'Todo') {
    //             this.$emit('todo', { option: item })
    //           } else if (action === 'Link' && item.linkConf.url) {
    //             window.open(item.linkConf.url, item.linkConf.target ? '_blank' : '')
    //           } else {
    //             this.$emit('action', item)
    //           }
    //         }
    //       }
    //     },
    //     this.$t(label)
    //   )
    // })
    return <el-col>
      <div
        class="advance-btn"
        style={ `padding-left: ${labelWidth}; text-align: ${position}` }
      >
        <el-button type="primary" onClick={this.saveForm}>{ name }</el-button>
        <el-button onClick={this.cancel}>{ cancelName }</el-button>
      </div>
    </el-col>
  }
}

/**
 * 
 */
function setValue (event, config, scheme, flag) {
  !flag && this.$set(config, 'defaultValue', event)
  this.$set(this[this.formConf.formModel], scheme.__vModel__, event)
}

/**
 * 
 */
function buildListeners (scheme) {
  const config = scheme.__config__
  const methods = scheme.on || []
  const listeners = {}

  // onkey-valuethisevent
  methods.forEach(key => {
    const { name, value } = key
    listeners[name] = event => evalFn(value).call(this, event)
  })
  //  render.js  vModel $emit('input', val)
  listeners.input = event => setValue.call(this, event, config, scheme)

  listeners.treeEmit = event => setValue.call(this, event, config, scheme, true)

  listeners.tableAction = event => this.$emit('tableAction', event)
  listeners.tableOption = event => this.$emit('tableOption', event)
  listeners.tableTodo = event => this.$emit('tableTodo', event)
  listeners.tableSel = event => this.$emit('tableSel', event)

  return listeners
}
function bindFunctionInAttr (scheme) {
  Object.keys(scheme).forEach(key => {
    if (typeof (scheme[key]) === 'function') {
      scheme[key] = scheme[key].bind(this)
    }
  })
}

/**
 * el-form
 */
function renderFrom (h) {
  const { formConf } = this
  return (
    <el-row gutter={formConf.gutter}>
      <el-form
        label-position={ formConf.labelPosition }
        label-width={ formConf.labelWidth + 'px' }
        ref={ formConf.formRef }
        rules={ this[formConf.formRules] }
        size={ formConf.size }
        disabled={ formConf.disabled }
        props={ { model: this[formConf.formModel] } }
      >
        { renderFormItem.call(this, h, formConf.fields) }
        { formBtns.call(this, h) }
      </el-form>
    </el-row>
  )
}

export default {
  name: 'lower-engine-parser',
  props: {
    formConf: {
      type: Object,
      required: true,
      default () {
        return {}
      }
    },
    type: {
      type: String,
      default: 'Page'
    },
    showBtns: {
      type: Boolean,
      default: true
    },
    mode: {
      type: String,
      default: 'Edit'
    },
    headers: {
      type: Function
    }
  },
  data () {
    const data = {
      [this.formConf.formModel]: {},
      [this.formConf.formRules]: {}
    }
    this.initFormData(this.formConf.fields, data[this.formConf.formModel])
    this.buildRules(this.formConf.fields, data[this.formConf.formRules])
    return data
  },
  methods: {
    // formConfig
    initFormData (list, formData) {
      list.forEach(item => {
        const config = item.__config__
        if (item.__vModel__) {
          // 
          if (config.tag === 'el-date-picker' && config.showCurrentTime && !config.defaultValue) {
            formData[item.__vModel__] = getCurrentDate(item)
          } else {
            formData[item.__vModel__] = config.defaultValue
          }
        }
        // stepactive
        if (config.tag === 'el-steps') {
          item.active = item.active > 1 ? 1 : item.active
        }
        bindFunctionInAttr.call(this, item)
        if (config.children) this.initFormData(config.children, formData)
        if (config.layout === 'mutilFormPage') {
          item.__slot__.options.forEach(m => this.initFormData(m.children, formData))
        }
      })
    },
    // rules
    buildRules (list, rules) {
      list.forEach(item => {
        const config = item.__config__
        if (Array.isArray(config.regList)) {
          if (config.required) {
            const required = { required: config.required, message: `${this.$t(config.label)}${this.$t('lower.ph95')}` }
            if (Array.isArray(config.defaultValue)) {
              required.type = 'array'
              required.message = `${this.$t('lower.ph96')}${this.$t(config.label)}`
            }
            config.regList.push(required)
          }
          rules[item.__vModel__] = config.regList.map(reg => {
            if (reg.pattern) {
              try {
                if (evalFn(reg.pattern) instanceof RegExp) {
                  reg.pattern = evalFn(reg.pattern)
                }
              } catch {
                setTimeout(() => {
                  this.$notify({
                    title: '',
                    type: 'warning',
                    message: `${this.$t(config.label)}(${item.__vModel__})${this.$t('lower.ph97')}`
                  })
                }, 500)
              }
            }
            reg.trigger = ruleTrigger && ruleTrigger[config.tag]
            return reg
          })
        }
        if (config.children) this.buildRules(config.children, rules)
        if (config.layout === 'mutilFormPage') {
          item.__slot__.options.forEach(m => this.buildRules(m.children, rules))
        }
      })
    },
    // 
    saveForm () {
      this.$emit('saveForm', this[this.formConf.formModel])
    },
    // 
    cancel () {
      this.$emit('cancel')
    },
    // 
    goBack (emit) {
      this.$emit('goBack', emit)
    }
  },
  components: {
    render
  },
  render (h) {
    return renderFrom.call(this, h)
  }
}
</script>
