export default {
  prepend (h, conf, key) {
    return <template slot="prepend">{conf.__slot__[key]}</template>
  },
  append (h, conf, key) {
    return <template slot="append">{conf.__slot__[key]}</template>
  },
  prefix (h, conf, key) {
    return <svg-icon icon-class={conf.__slot__[key]} slot="prefix"></svg-icon>
  },
  suffix (h, conf, key) {
    return <svg-icon icon-class={conf.__slot__[key]} slot="suffix"></svg-icon>
  }
}
