import { fetch, consts } from '@cseed/public-utils'
import get from 'lodash/get'
import cloneDeep from 'lodash/cloneDeep'
export default {
  data () {
    return {
      filterFields: [],
      filter: {},
      pagination: {}
    }
  },
  methods: {
    // 
    handleFilterChange (type) {
      // 
      consts.scrollToTop()
      if (type === 'query' && this.pagination) {
        this.pagination.currentPage = 1
      }
      this.fetchData()
    },
    // 
    handleSearch () {
      this.pagination.currentPage = 1
      // 
      let tempArr = []
      const cloneData = cloneDeep(this.$attrs.data)
      if (!this.dynamicConfig.url) {
        if (!Object.keys(this.filter).length) {
          tempArr = cloneData
        } else {
          for (const prop in this.filter) {
            tempArr = cloneData.filter(item => String(item[prop]).indexOf(this.filter[prop]) > -1)
          }
        }
        this.tableData.splice(0, this.tableData.length, ...tempArr)
        this.pagination.total = tempArr.length
      } else {
        // 
        this.fetchData()
      }
    },
    // 
    handleResetSearch () {
      this.filter = {}
      this.handleSearch()
    },
    // 
    handleRowSelectionAdd (targetItem, index) {
      this.tableSelect = this.tableSelect.concat(index)
      this.selected.splice(index, 1, targetItem)
      this.tableEmit(this.selected)
    },
    // 
    handleRowSingleAdd (targetItem, index) {
      this.tableSelect = index
      this.selected.splice(0, 1, targetItem)
      this.tableEmit(this.selected)
    },
    handleRowSelectionRemove (targetItem, index) {
      this.tableSelect = this.tableSelect.filter(item => item !== index)
      this.selected.splice(index, 1)
      this.tableEmit(this.selected)
    },
    // 
    handleAllRowSelectionChange (value) {
      if (value) {
        this.tableSelect = this.tableData.map((_, index) => index)
        this.selected = this.tableData.map(item => item)
      } else {
        this.tableSelect = []
        this.selected = []
      }
      this.tableEmit(this.selected)
    },
    tableEmit (sel) {
      this.$emit('tableSel', sel)
    },
    // 
    async handleOptions (options) {
      const optionsData = []
      for (let i = 0; i < options.length; i++) {
        const item = options[i]
        if (
          item.optionsApi &&
          item.optionsApi.type === 'dynamic' &&
          item.optionsApi.url &&
          item.optionsApi.dataPath &&
          item.optionsApi.label &&
          item.optionsApi.value
        ) {
          try {
            const httpReq = (this.baseIndex && this.baseIndex.fetchMethod) || fetch
            const httpData = await new Promise((resolve, reject) => {
              httpReq({
                url: item.optionsApi.url,
                method: item.optionsApi.method || 'get'
              }).then((data) => {
                const dataPath = item.optionsApi.dataPath
                const tempData = dataPath ? get(data, dataPath) : data
                const responceData = Array.isArray(tempData) ? tempData : []
                resolve(responceData)
              })
            })
            item.options = item.dataType === 'cascader'
              ? httpData
              : httpData.map(res => {
                return {
                  label: res[item.optionsApi.label],
                  value: res[item.optionsApi.value]
                }
              })
            optionsData.push(item)
          } catch (e) {
            console.error('', e)
          }
        } else {
          optionsData.push(item)
        }
      }
      return optionsData
    },
    actionOption (data) {
      this.$emit('action', data)
    },
    actionTodo (data) {
      this.$emit('todo', data)
    }
  }
}
