import validate from './validate'
/**
 * @param time:,
 * @param str:,yyyy/MM/dd,yyyy/MM/dd hh:mm:ss
 * yyyy 
 * MM   
 * dd   
 * tt   AM PM
 * HH    12
 * hh    24
 * mm   
 * ss   
 * fff    3
 *    yyyy/MM/dd hh:mm:ss
 * @returns {'yyyy/MM/dd hh:mm:ss'}
 */
function formatDate (time, str) {
  let date = time || new Date()
  if (!validate.isDate(date)) {
    date = new Date(typeof time === 'number' ? time : time.replace(/-/g, '/'))
  }
  let fmt = str || 'yyyy/MM/dd hh:mm:ss'
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  const hour = date.getHours()
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': hour,
    'H+': hour % 12 || 12,
    't+': hour < 12 ? 'AM' : 'PM',
    'm+': date.getMinutes(),
    's+': date.getSeconds(),
    fff: date.getMilliseconds()
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + ''
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? str : str.padStart(k.length, '0')
      )
    }
  }
  return fmt
}

/**
 * @param date:,str: 'yyyy-MM-dd hh:mm:ss''yyyy/MM/dd hh:mm'
 * @description str
 * @returns {yyyy-MM-dd hh:mm:ss}
 */
function formatDateBy (date, str) {
  let concatStr = str.indexOf('/') > -1 ? '/' : '-'
  // d1-111970d12-1616
  const time = isDate(date) ? date : new Date(date)
  let y = str.indexOf('yyyy') > -1 ? time.getFullYear() : '' // 
  let m =
    str.indexOf('MM') > -1
      ? (time.getMonth() + 1).toString().padStart(2, '0')
      : '' // 
  let d =
    str.indexOf('dd') > -1
      ? time
          .getDate()
          .toString()
          .padStart(2, '0')
      : '' // 
  let hh =
    str.indexOf('hh') > -1
      ? ' ' +
        time
          .getHours()
          .toString()
          .padEnd(2, '0')
      : '' // 
  let mm =
    str.indexOf('mm') > -1
      ? ':' +
        time
          .getMinutes()
          .toString()
          .padEnd(2, '0')
      : '' // 
  let ss =
    str.indexOf('ss') > -1
      ? ':' +
        time
          .getSeconds()
          .toString()
          .padEnd(2, '0')
      : '' // 
  return (
    `${y}` +
    concatStr +
    `${m}` +
    concatStr +
    `${d}` +
    `${hh}` +
    `${mm}` +
    `${ss}`
  )
}

/**
 * @param num (1234567)
 * @param len ,3
 * @param fill ""
 * @returns {'1,234,567'}
 */
function fillNumber (num, len, fill) {
  if (typeof num !== 'number') {
    return num
  }
  let nums = (num || 0).toString()
  let result = ''
  const index = len || 3
  while (nums.length > index) {
    result = (fill || ',') + nums.slice(-index) + result
    nums = nums.slice(0, nums.length - index)
  }
  if (nums) {
    result = nums + result
  }
  return result
}

/**
 *
 * @param string ('' '1234567890')
 * @param frontLen 
 * @param endLen 
 */
function hidden (str, frontLen, endLen) {
  if (str === undefined || str === null) {
    return ''
  }
  if (str.length === 2) {
    endLen = 0
  }
  var len = str.length - frontLen - endLen
  var xing = ''
  for (var i = 0; i < len; i++) {
    xing += '*'
  }
  return str.substring(0, frontLen) + xing + str.substring(str.length - endLen)
}

export default { formatDate, formatDateBy, fillNumber, hidden }