import qs from 'qs'
import dayjs from 'dayjs'

const TREE_NODE_ID = 'treeNodeId'

/**
 * 
 * @param {*} str
 * @returns
 */
export function isNumberStr (str) {
  return /^[+-]?(0|([1-9]\d*))(\.\d+)?$/g.test(str)
}

/**
 * ID
 * @returns
 */
export function getTreeNodeId () {
  const str = localStorage.getItem(TREE_NODE_ID)
  if (str) return parseInt(str, 10)
  return 100
}

/**
 * ID
 * @param {*} id
 */
export function setTreeNodeId (id) {
  localStorage.setItem(TREE_NODE_ID, id)
}

export function evalFn (fn) {
  const Fn = Function
  return new Fn('return ' + fn)()
}

/**
 * 
 * @param {*} str
 * @returns
 */
export function titleCase (str) {
  return str.replace(/( |^)[a-z]/g, L => L.toUpperCase())
}

/**
* 
* @param {*} key
* @param {*} value
* @returns 
*/
export function getQsData (key, value) {
  return qs.parse(`${key}=${value}`, {
    decoder (str) {
      // 
      if (/^\[/i.test(str)) {
        return JSON.parse(str)
      }
      return str
    }
  })
}

/**
 * items
 * typeof items -- string
 * typeof items -- function
 * typeof items -- Array
 * @returns
 */
export function getItems ({ items, config, fieldList = [] }) {
  let options = []
  if (typeof items === 'string') {
    switch (items) {
      case 'modelKeys':
        options = recursionModelKeys(fieldList, options).map(item => {
          return {
            label: item,
            value: item
          }
        })
        break
    }
  } else if (typeof items === 'function') {
    options = items(config)
  } else {
    options = items
  }
  return options
}

/**
 * key
 * @param list
 * @param options
 */
function recursionModelKeys (list, options) {
  list.forEach(item => {
    const config = item.__config__
    const slot = item.__slot__
    const vModel = item.__vModel__
    vModel && options.push(vModel)
    if (config.children) recursionModelKeys(config.children, options)
    //  el-step el-tabs
    slot && slot.options && slot.options.forEach(s => {
      if (s.children) recursionModelKeys(s.children, options)
    })
  })
  return options
}

/**
 * dataMapping
 * @param {*} config
 * @returns
 */
export function getDataCol (config) {
  const { dataMapping } = config.__config__
  return dataMapping ? config[dataMapping] : config.__slot__.options
}

/**
 * label value children
 * @param {*} param0
 * @returns
 */
export function recursionData ({ data, labelMap, valueMap, children }) {
  data.forEach(item => {
    if (item[labelMap]) item.label = item[labelMap]
    if (item[valueMap]) item.value = item[valueMap]
    if (item[children] && item[children].length > 0) {
      item.children = recursionData({ data: item[children], labelMap, valueMap, children })
    }
  })
  return data
}

/**
 * el-input number string boolean
 * @param {*} value
 */
export function reverseValue (value) {
  let newValue
  if (Array.isArray(value)) {
    newValue = value.join(',')
  } else if (typeof value === 'boolean' || typeof value === 'object') {
    newValue = JSON.stringify(value)
  } else {
    newValue = value
  }
  return newValue
}

/**
 * input
 * number string boolean
 * @param {*} value
 */
export function setOptionValueType (item, val) {
  if (/^true$/i.test(val)) {
    item.value = true
  } else if (/^false/i.test(val)) {
    item.value = false
  } else {
    item.value = isNumberStr(val) ? +val : val
  }
}

/**
 *  YYYY-MM-DD
 * @returns
 */
export function getCurrentDate (config) {
  const format = config.format === 'yyyy  WW ' ? config.format.replace('yyyy  WW ', 'yyyy-MM-DD') : config.format.replace(/dd/, 'DD')
  return dayjs().format(format)
}

/**
 * 
 * @param {*} param0
 * @returns
 */
export function readListRecursion ({ list, keyval, temp, labelMap, valueMap }) {
  list.forEach(item => {
    keyval.forEach(v => {
      if (Array.isArray(v) && v.indexOf(item[valueMap]) > -1) {
        temp.push(item[labelMap])
      }
    })
    if (keyval.indexOf(item[valueMap]) > -1) {
      temp.push(item[labelMap])
    }
    if (item.children) readListRecursion({ list: item.children, keyval, temp, labelMap, valueMap })
  })
  return Array.from(new Set(temp))
}

/**
 *  string
 * @param {*} list
 */
export function processFnToStr (list) {
  list.forEach(item => {
    if (item instanceof Object) {
      for (const prop in item) {
        if (typeof item[prop] === 'function') {
          item[prop] = item[prop].toString()
        } else if (item[prop] instanceof Object) {
          processFnToStr([item[prop]])
        } else if (item[prop] instanceof Array) {
          processFnToStr(item[prop])
        }
      }
    }
  })
  return list
}

/**
 * function
 * @param {*} list
 */
export function processStrToFn (list) {
  list.forEach(item => {
    if (item instanceof Object) {
      for (const prop in item) {
        if (/^function/.test(item[prop])) {
          item[prop] = evalFn(item[prop])
        } else if (item[prop] instanceof Object) {
          processStrToFn([item[prop]])
        } else if (item[prop] instanceof Array) {
          processStrToFn(item[prop])
        }
      }
    }
  })
  return list
}

/**
 *  
 * @param {*} fileName
 */
export function matchFileType (fileName) {
  let suffix = '' // 
  let result = '' // 
  try {
    const fileList = fileName.split('.')
    suffix = fileList[fileList.length - 1]
  } catch (e) {
    suffix = ''
  }
  if (!suffix) {
    result = false
    return result
  }
  // txt
  const txtlist = ['txt']
  result = txtlist.some(function (item) {
    return item === suffix
  })
  if (result) {
    result = 'txt'
    return result
  }

  // txt
  const excelist = ['xls', 'xlsx']
  result = excelist.some(function (item) {
    return item === suffix
  })
  if (result) {
    result = 'excel'
    return result
  }

  // txt
  const wordlist = ['doc', 'docx']
  result = wordlist.some(function (item) {
    return item === suffix
  })
  if (result) {
    result = 'word'
    return result
  }

  //  pdf
  const pdflist = ['pdf']
  result = pdflist.some(function (item) {
    return item === suffix
  })
  if (result) {
    result = 'pdf'
    return result
  }
  //  ppt
  const pptlist = ['ppt']
  result = pptlist.some(function (item) {
    return item === suffix
  })
  if (result) {
    result = 'ppt'
    return result
  }

  result = 'other'

  return result
}
