<template>
  <c-card
    ref="contentCard"
    class="content"
    :title="title || undefined"
    @go-back="cancel"
  >
    <Parser
      class="customForm"
      ref="parser"
      v-if="key"
      :key="key"
      :mode="mode"
      :form-conf="lowerJson"
      :headers="_headers"
      :fetchMethod="fetchMethod"
      @goBack="goBack"
      @tableAction="tableAction"
      @tableOption="tableOption"
      @tableTodo="tableTodo"
      @tableSel="tableSel"
      @cancel="cancel"
      @saveForm="savePage"
    >
      <template v-for="name of slotCol" v-slot:[name]>
        <slot :name="name" v-bind="{form}"></slot>
      </template>
    </Parser>
  </c-card>
</template>
<script>
import setExtraMixin from './SetExtra'
import { advanceForm, createSandbox } from '@cseed/public-utils'
import { CCard } from '@cseed/admin-toolkit'
import Parser from '../parser/Index.vue'
import get from 'lodash/get'
export default {
  props: ['lowerJson', 'mode', 'title', 'row', '_headers', 'fetchMethod'],
  inject: ['baseIndex'],
  mixins: [setExtraMixin],
  data () {
    return {
      key: null,
      slotCol: [], // 
      form: {}
    }
  },
  created () {
    this.parseFormField()
  },
  methods: {
    async parseFormField () {
      const { formModel, formRef, fields } = this.lowerJson
      try {
        //  
        await advanceForm.setInterfaceData(fields, { fetch: this.baseIndex.fetchMethod, form: this.row })
      } catch (err) {
        console.warn(err)
        this.$notify.error(this.$t('lower.ph103'))
      }
      if (this.row) {
        // 
        this.fetchData()
      } else {
        this.setExtraAttr({ formModel, formRef, fields })
        this.key = +new Date()
      }
    },
    // 
    async fetchData () {
      const params = {}
      const data = {}
      const { fetchApiName, fetchDataFormat, formModel, formRef, fields, fetchApi = {}, isCloseDetailApi = false } = this.lowerJson
      const { url, method, extraParams, dataPath } = fetchApi
      // url
      if (extraParams && Object.keys(extraParams).length) {
        for (const prop in extraParams) {
          if (method === 'GET') {
            params[prop] = this.row[extraParams[prop]]
          } else {
            data[prop] = this.row[extraParams[prop]]
          }
        }
      }
      // 
      if (isCloseDetailApi) {
        this.baseIndex.fetchMethod({
          url: fetchApiName || url,
          method: method || 'GET',
          params
        }).then((data) => {
          // 
          data = dataPath ? get(data, dataPath) : data instanceof Array ? data : []
          commonFn.call(this, { data })
        }).catch(err => {
          if (err.code === '404') {
            this.$message.error('')
          }
        })
      } else {
        commonFn.call(this, { data: this.row })
      }

      function commonFn ({ data } = {}) {
        /**
         * todo.js 
         *  
         * todo.js
         */
        if (fetchDataFormat) {
          try {
            if (typeof this.baseIndex[fetchDataFormat] === 'function') {
              data = this.baseIndex[fetchDataFormat].call(this, data, this.lowerJson, this.row)
            } else {
              data = createSandbox.call(this, { code: fetchDataFormat, data, json: this.lowerJson, row: this.row })
            }
          } catch (err) {
            console.warn(err)
          }
        }
        this.form = data
        // 
        this.$emit('fetchData', data)
        this.setFillFormData({
          fields,
          formModel,
          formRef,
          formData: data
        })
        // 
        this.key = +new Date()
      }
    },
    tableAction (option) {
      this.$emit('action', option)
    },
    tableOption (option) {
      this.$emit('option', option)
    },
    tableTodo (option) {
      this.$emit('todo', option)
    },
    tableSel (selected) {
      this.$emit('selected', selected)
    },
    cancel () {
      this.$emit('go-back')
    },
    // 
    savePage (data) {
      let formData = JSON.parse(JSON.stringify(data))
      const { formRef, saveFormFormat, saveApiName, dataSubmitType = 'json', saveApi } = this.lowerJson
      const { url, method, refreshKey = true } = saveApi
      /**
       * todo.js 
       *  
       * todo.js
       */
      if (saveFormFormat) {
        try {
          if (typeof this.baseIndex[saveFormFormat] === 'function') {
            formData = this.baseIndex[saveFormFormat].call(this, formData, this.lowerJson)
          } else {
            formData = createSandbox.call(this, { code: saveFormFormat, data: formData, json: this.lowerJson })
          }
        } catch (err) {
          console.warn(err)
        }
      }
      if (this.row) {
        formData = Object.assign({}, this.form, formData)
      }
      let isFile = false
      //  json / formData
      if (dataSubmitType === 'formData') {
        const submitData = new FormData()
        for (const prop in formData) {
          submitData.append(prop, formData[prop])
        }
        isFile = true
        formData = submitData
      }

      this.$refs.parser.$refs[formRef].validate(valid => {
        if (valid) {
          this.baseIndex.fetchMethod({
            method: method || 'POST',
            url: saveApiName || url,
            data: formData,
            isFile
          }).then(() => {
            this.$message.success('')
            this.$emit('go-back', refreshKey)
          })
        }
      })
    },
    goBack (emit) {
      this.$emit('go-back', emit && emit.flag)
    }
  },
  components: {
    Parser,
    CCard
  }
}
</script>
