<!--
  /*
 * @Author: lc
 * @Date: 2022-04-21 12:26:45
 * @Last Modified by: lc
 * @Last Modified time: 2023-02-21 16:14:52
 * @Description: admin-toolkit table
 * @url: http://172.20.155.102/admin-toolkit/components/TableTree.html
 * v-lower-ref:  
 */
-->

<template>
  <c-content-wrapper
    :pagination="showPagination"
    @filter-change="handleFilterChange"
    v-lower-ref="(dom) => !isDesign && setChildrenRef('cc-lower-table', dom)"
  >
    <!--  -->
    <div class="list-top">
      <Actions
        v-if="actionBar && actionBar.length"
        :actions="actionBar"
        :rows="tableHeader"
        :is-design="isDesign"
        @action="actionOption"
        @todo="actionTodo"
        class="action-list"
      />
      <CListFilter
        ref="advance-expandForm"
        :options="filterFields"
        :form-data="filter"
        :length="searchNum || 3"
        v-if="!expand && filterFields.length > 0"
        :hidden-expand-button="true"
        :is-expand="expand"
        @filter="handleSearch"
        @reset="handleResetSearch"
        @filter-expand="handleFilterExpand"
      />
    </div>
    <div
      class="list-top-expand"
      v-if="expand && filterFields.length > 0"
    >
      <CListFilter
        ref="advance-expandForm"
        :options="filterFields"
        :form-data="filter"
        :length="-1"
        :is-expand="expand"
        :hidden-expand-button="true"
        @filter="handleSearch"
        @reset="handleResetSearch"
        @filter-expand="handleFilterExpand"
      />
    </div>
    <c-table-tree
      ref="advance-table"
      :props="$attrs.tableProps"
      :header="tableHeader"
      :data="tableData"
      :selection-type="$attrs.selectionType"
      :selected="tableSelect"
      @row-selection-add="handleRowSelectionAdd"
      @row-selection-remove="handleRowSelectionRemove"
      @row-selection-change="handleRowSingleAdd"
      @all-row-selection-change="handleAllRowSelectionChange"
    >
      <template slot="empty">
        <el-empty
          :image="emptyImg"
          :image-size="200"
          :description="$attrs.tableProps.emptyText"
        />
      </template>
    </c-table-tree>
  </c-content-wrapper>
</template>
<script>
import { advanceForm, fetch, consts, format, createSandbox } from '@cseed/public-utils'
import { CTableTree, CContentWrapper } from '@cseed/admin-toolkit'
import dayjs from 'dayjs'
import CListFilter from '../component/CListFilter.vue'
import BaseList from './BaseList'
import { commonOperation } from './RenderMethods'
import Actions from './Actions.vue'
import cloneDeep from 'lodash/cloneDeep'

const pageInfo = { currentPage: 1, pageSize: 10 }
// 
const localRenderReg = /^\$\{(.*?)\}$/gm
export default {
  name: 'AdvanceBasicTable',
  extends: BaseList,
  inject: {
    baseIndex: {
      form: 'baseIndex',
      default: () => {
        return null
      }
    },
    isDesign: {
      type: Boolean,
      default: false
    },
    // 
    setChildrenRef: {
      default: () => {}
    }
  },
  data () {
    return {
      selected: [],
      tableSelect: [],
      expand: false,
      searchNum: 3,
      emptyImg: require('./empty.png')
    }
  },
  computed: {
    tableHeader () {
      const _this = this.baseIndex || this
      const props = this.$attrs.tableProps
      const operate = this.$attrs.operate || {}
      const fields = this.$attrs.header
      const operateRow = cloneDeep(this.$attrs.operateRow) // 
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.filterFields = consts.disposalField(fields, 2)
      const listHeader = consts.disposalField(fields, 1)
      listHeader && listHeader.forEach((item, key) => {
        if (item.render) {
          try {
            // render
            if (localRenderReg.test(item.render)) {
              const dynamic = item.render.replace(localRenderReg, (m, k) => k)
              if (dynamic && _this.handleResource) {
                const mthods = _this.handleResource.renderMethods
                item.render = mthods[dynamic]
                  ? mthods[dynamic].call(_this, { item, prop: item.prop })
                  : this.initRender({ item })
              } else {
                item.render = this.initRender({ item })
              }
            } else {
              item.render = createSandbox.call(_this, { code: item.render, item, prop: item.prop })
            }
          } catch (err) {
            console.warn(err)
            item.render = this.initRender({ item })
          }
        } else {
          if (item.dataType && item.dataType !== 'string') {
            const renderMap = {
              // 
              enum: function (h, { row }) {
                const optionsText = {}
                const tagType = ['', 'success', 'warning', 'danger', 'info']
                item.options.forEach((option, idx) => {
                  optionsText[option.value] = {
                    label: option.label,
                    type: tagType[idx]
                  }
                })
                let tags = [row[item.prop]]
                if (typeof row[item.prop] === 'string') {
                  tags = row[item.prop].split(',')
                }
                const tagVNodes = tags.filter(key => optionsText[key] !== undefined).map(key => h(
                  'el-tag',
                  {
                    props: {
                      type: optionsText[key].type || ''
                    }
                  },
                  optionsText[key].label
                ))
                return h('div', { style: { lineHeight: '32px' } }, tagVNodes)
              },
              // 
              number: function (h, { row }) {
                let num = row[item.prop]
                if (typeof num === 'string') {
                  num = parseFloat(num)
                }
                return h(
                  'span',
                  {},
                  format.fillNumber(num, 3, ','))
              },
              // 
              image: function (h, { row }) {
                return h(
                  'el-image',
                  {
                    attrs: {
                      src: row[item.prop],
                      fit: 'contain',
                      'preview-src-list': [row[item.prop]]
                    },
                    style: {
                      height: '50px'
                    }
                  },
                  ''
                )
              },
              // 
              date: function (h, { row }) {
                const format = item.format || 'YYYY-MM-DD HH:mm:ss'
                return h(
                  'span',
                  {},
                  dayjs(row[item.prop]).format(format)
                )
              },
              // 
              progress: function (h, { row }) {
                const percentage = row[item.prop] > 100 ? 100 : row[item.prop]
                return h(
                  'el-progress',
                  {
                    props: {
                      percentage,
                      showText: true
                    }
                  },
                  ''
                )
              },
              // html
              html: function (h, { row }) {
                return h(
                  'div',
                  {
                    domProps: {
                      innerHTML: row[item.prop]
                    }
                  },
                  ''
                )
              }
            }
            item.render = renderMap[item.dataType]
          } else {
            item.render = function (h, { row }) {
              if (!row[item.prop]) {
                return h('span', {}, props.lackValueFill ? props.lackValueFill : '')
              } else {
                return h('span', {}, row[item.prop])
              }
            }
          }
        }
        // tips
        if (item.headerTip) {
          item.renderHeader = (h, { column }) => {
            return [
              column.label,
              h('el-tooltip',
                {
                  props: {
                    placement: 'top-start',
                    effect: item.effect || 'dark'
                  }
                },
                [
                  h('div',
                    {
                      slot: 'content'
                    },
                    [item.headerTip]
                  ),
                  h('span', {
                    class: {
                      'el-icon-question': true
                    },
                    style: {
                      padding: '0 10px',
                      cursor: 'pointer'
                    }
                  })
                ]
              )
            ]
          }
        } else {
          delete item.renderHeader
        }
        // sortable: true -> sort-method
        if (item.sortMethodMap && _this.baseIndex) {
          const sortTodo = _this.baseIndex.$refs.list[item.sortMethodMap]
          if (typeof sortTodo === 'function') {
            item.sortMethod = (a, b) => {
              return sortTodo(a, b)
            }
          }
        } else {
          delete item.sortMethod
        }
      })
      if (operateRow && operateRow.length) {
        listHeader.push({
          label: this.$t(operate.name || 'lower.operate'),
          width: parseInt(operate.width || props.operateWidth),
          fixed: operate.fixed ? 'right' : false,
          render: commonOperation.call(this, operateRow, this.isDesign, operate)
        })
      }
      return listHeader
    },
    dynamicConfig () {
      return this.$attrs.dynamicConfig
    },
    tableData: {
      get () {
        // 
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.pagination = this.dynamicConfig.pagination || pageInfo
        return cloneDeep(this.$attrs.data) || []
      },
      set (val) {
        return val || []
      }
    },
    actionBar () {
      return this.$attrs.actionBar
    },
    operateRow () {
      return this.$attrs.operateRow
    },
    showPagination () {
      return this.dynamicConfig.pagination.show ? this.pagination : null
    }
  },
  methods: {
    handleFilterExpand (expand) {
      this.expand = expand
    },
    // sortChange ({ column, prop, order }) {
    //   console.log(column)
    //   console.log(prop)
    //   console.log(order)
    // },
    async fetchData () {
      const config = {
        __config__: this.$attrs.__config__,
        dynamicConfig: this.dynamicConfig,
        data: this.$attrs.data
      }
      const filter = this.filter || {}
      const fetchAjax = (this.baseIndex && this.baseIndex.fetchMethod) || fetch
      if (this.dynamicConfig.url) {
        const row = (this.baseIndex && this.baseIndex.$parent.row) || {}
        const { data = [] } = await advanceForm.fetchDynamic(config, fetchAjax, row, filter)
        this.tableData.splice(0, this.tableData.length, ...data)
      }
    },
    initRender ({ item }) {
      return function (h, { row }) {
        return h('span', {}, row[item.prop])
      }
    }
  },
  created () {
    this.pagination = this.dynamicConfig.pagination
    if (this.$attrs.search) {
      this.expand = this.$attrs.search.expandAll
      this.searchNum = this.$attrs.search.showNum
    }
    this.expand = this.$attrs.search && this.$attrs.search.expandAll
    this.handleOptions(this.$attrs.header).then(res => {
      this.filterFields = consts.disposalField(res, 2)
    })
  },
  components: {
    CListFilter,
    Actions,
    CTableTree,
    CContentWrapper
  }
}
</script>
<style lang="stylus" scoped>
.list-top
  padding 0 0 5px 0
  &:after
    display block
    content ''
    height 0
    clear both
  .action-list
    float left
    margin-right 14px
    padding-bottom 10px
.list-top-expand
  padding 0 0 5px 0
  &:after
    display block
    content ''
    height 0
    clear both
  >>>.btn_group
    padding-bottom 10px
>>> .el-image__error
  padding: 10px
  font-size: 12px
</style>
