import { get } from 'lodash'
import qs from 'qs'

function getQsData (key, value) {
  return qs.parse(`${key}=${value}`, {
    delimiter: 'lower_decode_coocaa!@#$', // lower_decode_coocaa!@#$qs&
    decoder (str) {
      // 
      if (/^\[/i.test(str)) {
        return JSON.parse(str)
      }
      return str
    }
  })
}

/**
 * label value
 * @param {*} param0
 * @returns
 */
function recursionData ({ data, labelMap, valueMap, children }) {
  data.forEach(item => {
    if (item[labelMap]) item.label = item[labelMap]
    if (item[valueMap]) item.value = item[valueMap]
    if (item[children] && item[children].length > 0) {
      item.children = recursionData({ data: item[children], labelMap, valueMap, children })
    }
  })
  return data
}

/**
 * -api
 * @param fields
 */
async function setInterfaceData (fields, { fetch, form }) {
  for (let i = 0; i < fields.length; i++) {
    let item = fields[i]
    const config = item.__config__
    if (config.dataType === 'dynamic') {
      await fetchDynamic(item, fetch, form)
    } else {
      if (config.children) setInterfaceData(config.children, { fetch })
    }
  }
}

function fetchDynamic (item, fetch, form, filter) {
  const { dataMapping = '__slot__[options]' } = item.__config__
  const { url, method, dataPath, labelMap = 'label', valueMap = 'value', pagination, extraParams = '' } = item.dynamicConfig
  const children = (item.props && item.props.props) || 'children'
  let params = {} // GET
  let data = {} // POST
  if (pagination) {
    const { pageAlias = 'page', sizeAlias = 'pageSize', currentPage = 1, pageSize = 10 } = pagination
    params = {
      [pageAlias]: currentPage,
      [sizeAlias]: pageSize
    }
  }
  // 
  if (filter) {
    if (method === 'GET') {
      params = Object.assign({}, params, filter)
    } else {
      data = Object.assign({}, data, filter)
    }
  }
  // url
  if (form && extraParams && Object.keys(extraParams).length) {
    for (let prop in extraParams) {
      if (method === 'GET') {
        params[prop] = form[extraParams[prop]]
      } else {
        data[prop] = form[extraParams[prop]]
      }
    }
  }
  if (url && method) {
    return new Promise(resolve => {
      fetch({
        url,
        method,
        params,
        data
      }).then(res => {
        if (pagination && pagination.totalPath) pagination.total = get(res, pagination.totalPath) || 0
        const data = dataPath ? get(res, dataPath) : res instanceof Array ? res : []
        // TODO 
        let options = recursionData({ data, labelMap, valueMap, children })
        let qsData = getQsData(dataMapping, JSON.stringify(options))
        Object.assign(item, qsData)
        resolve(item)
      })
    })
  }
}

/**
 * 
 * @param {*} str
 * @returns
 */
function isNumberStr (str) {
  return /^[+-]?(0|([1-9]\d*))(\.\d+)?$/g.test(str)
}

/**
 *   0 false
 * @param {*} num 
 */
function isTrusData (num) {
  let flag = true
  if (typeof num === 'undefined' || num === null) {
    flag = false
  } else if (typeof num === 'number' && isNaN(num) ) {
    flag = false
  }
  return flag
}

export default {
  setInterfaceData,
  isNumberStr,
  fetchDynamic,
  getQsData,
  isTrusData
}