<!--  -->
<template>
  <div class="filter-form">
    <c-form
      @submit.native.prevent
      @keyup.native.enter="handleFilter"
      :inline="true"
    >
      <template v-for="(item, key) of formItems">
        <!--  -->
        <c-form-string
          v-if="!item.dataType || item.dataType === 'string'"
          :label="item.showLabel ? item.label : ''"
          :key="key"
          :placeholder="item.filterPh || `${$t('cPlaceholder.pleaseEnters',[$t(item.label)])}`"
          v-model="formData[item.prop]"
          clearable
          @change="item.filterTrigger && $emit('filter')"
        />
        <!--  -->
        <el-form-item
          v-if="item.dataType === 'number'"
          :key="key"
          :label="item.showLabel ? item.label : ''"
        >
          <el-input-number
            class="advance-inpt-number"
            :placeholder="item.filterPh || `${$t('cPlaceholder.pleaseEnters',[$t(item.label)])}`"
            v-model="formData[item.prop]"
            controls-position="right"
            clearable
            @change="item.filterTrigger && $emit('filter')"
          />
        </el-form-item>
        <!--  -->
        <c-form-enum
          v-if="item.dataType === 'enum'"
          :label="item.showLabel ? item.label : ''"
          :key="key"
          v-model="formData[item.prop]"
          :options="item.options"
          :placeholder="item.filterPh ||  `${$t('cPlaceholder.pleaseSelects',[$t(item.label)])}`"
          filterable
          clearable
          @change="item.filterTrigger && $emit('filter')"
        />
        <!--  -->
        <el-form-item
          v-if="item.dataType === 'cascader'"
          :key="key"
          :label="item.showLabel ? item.label : ''"
        >
          <el-cascader
            :options="item.options"
            v-model="formatForm[item.prop]"
            :placeholder="item.filterPh || `${$t('cPlaceholder.pleaseSelects',[$t(item.label)])}`"
            :show-all-levels="false"
            clearable
            @change="changeCascader({ prop: item.prop, trigger: item.filterTrigger })"
            :props="{
              label: item.optionsApi.label || 'label',
              value: item.optionsApi.value || 'value',
              children: item.optionsApi.children || 'children',
              checkStrictly: true
            }"
          />
        </el-form-item>
        <!--  -->
        <c-form-any
          v-if="item.dataType === 'date'"
          :key="key"
          :label="item.showLabel ? item.label : ''"
        >
          <div slot="edit">
            <el-date-picker
              v-if="item.dateProp && item.dateProp.isRange"
              v-model="formatForm[item.prop]"
              @change="changeDateRange(item.prop, item.dateProp, item.filterTrigger)"
              :type="item.dateProp.showTime ? 'datetimerange' : 'daterange'"
              range-separator="-"
              :start-placeholder="$t('cMessage.startTime')"
              :end-placeholder="$t('cMessage.endTime')"
              :value-format="dateFilter(item.format)"
              :picker-options="pickerOptions(item.dateProp)"
            />
            <el-date-picker
              v-else
              :type="item.dateProp.showTime ? 'datetime' : 'date'"
              v-model="formData[item.prop]"
              :value-format="dateFilter(item.format)"
              :placeholder="item.filterPh || `${$t('cPlaceholder.pleaseSelects',[$t(item.label)])}`"
            />
          </div>
        </c-form-any>
      </template>
      <!--  -->
      <div
        class="btn_group"
        v-if="formItems.length"
      >
        <el-button-group>
          <el-button
            size="small"
            @click="handleFilter"
          >
            {{ genButtonText.query }}
          </el-button>
          <el-button
            size="small"
            @click="handleFilterReset"
          >
            {{ genButtonText.reset }}
          </el-button>
          <template v-if="options.length > length">
            <el-button
              size="small"
              v-if="!expand"
              @click="handleFilterExpand"
            >
              {{ genButtonText.expand }}
            </el-button>
            <el-button
              size="small"
              v-if="expand"
              @click="handleFilterExpand"
            >
              {{ genButtonText.packUp }}
            </el-button>
          </template>
        </el-button-group>
      </div>
    </c-form>
  </div>
</template>

<script>
import { CForm, CFormString, CFormEnum, CFormAny } from '@cseed/admin-toolkit'
import { quickComList } from '../static/index'
export default {
  components: { CForm, CFormString, CFormEnum, CFormAny },
  data () {
    return {
      filter: {},
      expand: '',
      formatForm: {},
      test: 'change'
    }
  },
  props: {
    options: {
      type: Array,
      default: () => {
        return []
      }
    },
    formData: {
      type: Object,
      default: () => {
        return {}
      }
    },
    isExpand: {
      type: Boolean,
      default: false
    },
    length: {
      type: Number,
      default: 3
    }
  },
  computed: {
    formItems () {
      if (this.length > 0) {
        return this.options.slice(0, this.length)
      } else {
        return this.options
      }
    },
    genButtonText () {
      return {
        query: this.$t('cButton.query'),
        reset: this.$t('cButton.reset'),
        expand: this.$t('cButton.expand'),
        packUp: this.$t('cButton.packUp'),
        ...this.buttonText
      }
    }
  },
  methods: {
    changeDateRange (name, item, trigger) {
      const arr = JSON.parse(JSON.stringify(this.formatForm[name]))
      this.$set(this.formData, item.startKey || 'startDate', arr && arr.length === 2 ? arr[0] : '')
      this.$set(this.formData, item.endKey || 'endDate', arr && arr.length === 2 ? arr[1] : '')
      if (trigger) this.handleFilter()
    },
    changeCascader ({ prop, trigger } = {}) {
      const arr = JSON.parse(JSON.stringify(this.formatForm[prop]))
      if (arr) {
        this.$set(this.formData, prop, arr.pop())
      }
      if (trigger) {
        this.handleFilter()
      }
    },
    treeHandle (data) {
      return data
        ? data.map((item) => {
          if (item.children.length) {
            this.$set(item, 'children', this.treeHandle(item.children))
          } else {
            this.$delete(item, 'children')
          }
          return item
        })
        : []
    },
    handleFilter () {
      this.$emit('filter')
    },
    handleFilterReset () {
      this.formatForm = {}
      this.$emit('reset')
    },
    handleFilterExpand () {
      this.expand = !this.expand
      this.$emit('filter-expand', this.expand)
    },
    // dayformartelement
    dateFilter (format) {
      return format ? format.replace('YYYY', 'yyyy').replace('DD', 'dd') : 'yyyy-MM-dd'
    },
    // ++
    pickerOptions (props) {
      const _this = this
      let disabledDate = null
      const { quickOption, quickDefine } = props
      const shortcuts = quickComList.call(this, quickOption)
      quickDefine.length && quickDefine.forEach(quick => {
        shortcuts.push({
          text: this.$t(quick.name),
          onClick (picker) {
            if (!quick.start || !quick.end) {
              _this.$notify.error(_this.$t(''))
              return
            }
            const start = new Date()
            // eslint-disable-next-line no-eval
            const quickS = eval(quick.start)
            // eslint-disable-next-line no-eval
            const quickE = eval(quick.end)
            start.setTime(quickS)
            picker.$emit('pick', [start, quickE])
          }
        })
      })
      // 
      disabledDate = props.limit === 1
        ? (time) => {
            return time.getTime() < Date.now() - 8.64e7
          }
        : props.limit === 2
          ? (time) => {
              return time.getTime() > Date.now()
            }
          : null
      return {
        shortcuts,
        disabledDate
      }
    }
  },
  created () {
    this.expand = this.isExpand
  }
}
</script>
<style lang="stylus" scoped>
>>>.el-form
  flex-wrap wrap
  flex-direction row
.btn_group
  padding 0
  line-height 32px
  text-align right
.advance-inpt-number
  width: 220px
  >>>.el-input__inner
    text-align: left
.filter-form
  float left
  &:after
    clear both
    content ''
    display block
    height 0
  .data-form
    float left
  .btn_group
    float left
  /deep/.el-form-item--small.el-form-item
    padding 0 0 10px 0
    margin-bottom 0
</style>
