<template>
  <div class="upload-container">
    <el-button
      icon="el-icon-upload"
      size="mini"
      round
      v-if="upload.show"
      @click="dialogVisible=true"
    >
      {{ upload.title }}
    </el-button>
    <el-dialog :visible.sync="dialogVisible" append-to-body>
      <el-upload
        class="editor-slide-upload"
        :file-list="fileList"
        :headers="headers"
        :data="upload.extraData"
        :on-remove="handleRemove"
        :on-success="handleSuccess"
        :before-upload="beforeUpload"
        :action="upload.action"
        :disabled="!upload.action"
      >
        <el-button class="upoload-btn" size="small" type="primary" :disabled="!upload.action"></el-button>
      </el-upload>
      <el-button size="small" type="primary" @click="handleSubmit"></el-button>
      <el-button size="small" @click="dialogVisible = false"></el-button>
    </el-dialog>
  </div>
</template>

<script>
import get from 'lodash/get'
export default {
  name: 'EditorSlideUpload',
  props: {
    color: {
      type: String,
      default: '#1890ff'
    },
    upload: {
      type: Object,
      default: () => {
        return {
          show: false,
          title: 'Upload',
          action: '/violet-api/filemanage/file/upload',
          dataMap: 'data',
          extraData: {}
        }
      }
    }
  },
  data () {
    return {
      dialogVisible: false,
      listObj: {},
      fileList: [],
      headers: {}
    }
  },
  created () {
    this.headers = this.$headers && this.$headers()
  },
  methods: {
    checkAllSuccess () {
      return Object.keys(this.listObj).every(item => this.listObj[item].hasSuccess)
    },
    handleSubmit () {
      const arr = Object.keys(this.listObj).map(v => this.listObj[v])
      if (!this.checkAllSuccess()) {
        this.$message('!!!')
        return false
      }
      this.$emit('successCBK', arr)
      this.dialogVisible = false
      this.listObj = {}
      this.fileList = []
    },
    handleSuccess (response, file) {
      if (
        response.success ||
        response.code === 1000 ||
        response.code === '1000' ||
        response.code === 200
      ) {
        const uid = file.uid
        const objKeyArr = Object.keys(this.listObj)
        objKeyArr.forEach(o => {
          if (parseInt(o) === uid) {
            this.listObj[o].url = get(response, this.upload.dataMap)
            this.listObj[o].hasSuccess = true
          }
        })
      } else {
        this.$notify({
          type: 'error',
          message: response.msg || ''
        })
        this.handleRemove(file)
        const index = this.fileList.findIndex(v => v.uid === file.uid)
        this.fileList.splice(index, 1)
      }
    },
    handleRemove (file) {
      const uid = file.uid
      const objKeyArr = Object.keys(this.listObj)
      for (let i = 0, len = objKeyArr.length; i < len; i++) {
        if (this.listObj[objKeyArr[i]].uid === uid) {
          delete this.listObj[objKeyArr[i]]
          return
        }
      }
    },
    beforeUpload (file) {
      const imgLimit = ['image/jpeg', 'image/jpg', 'image/png', 'image/gif']
      const videoLimit = ['video/mp4', 'audio/mp4']
      const isImage = imgLimit.includes(file.type)
      const isMedia = videoLimit.includes(file.type)
      this.listObj[file.uid] = {
        name: file.name,
        type: isImage ? 'image' : isMedia ? 'video' : 'appendix',
        hasSuccess: false,
        uid: file.uid
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.editor-slide-upload {
  width: 100%;
  margin-bottom: 20px;
  ::v-deep .el-upload {
    width: 100%;
  }
  .upoload-btn {
    background-color: #fbfdff;
    border: 1px dashed #c0ccda;
    border-radius: 6px;
    box-sizing: border-box;
    width: 100%;
    height: 148px;
    line-height: 148px;
    vertical-align: top;
    text-align: center;
    color: #409EFF;
  }
}
.addEnumParams
  background #f5f5f5
  padding 10px 10px 0
</style>
