const commonOperation = {
  CREATE: 'add',
  UPDATE: 'update',
  DELETE: 'delete',
  READ: 'read',
  LIST: 'select'
}

const commonOperationEvent = {
  add: 'create',
  update: 'edit',
  delete: 'delete',
  read: 'read'
}

/** component vuejs 
 *  row
 * actionsAndPermissions ={handleSendAudit: ['', 'moviePush:sendAudit']}
 * add by wanghaihua
 * */
function createOperationRender (component, row, actionsAndPermissions) {
  let actions = {}
  let permissions = {}
  let disabledMap = {}
  let keys = Object.keys(actionsAndPermissions)
  keys.forEach((e) => {
    actions[e] = actionsAndPermissions[e][0]
    permissions[e] = actionsAndPermissions[e][1]
    disabledMap[e] = actionsAndPermissions[e][2]
  })
  let h = component.$createElement
  return Object.keys(actions).map(key => {
    return h(
      'el-button', {
        directives: [{
          name: 'permission',
          value: permissions[key]
        }],
        props: {
          type: 'text',
          plain: false,
          size: 'mini',
          disabled: disabledMap[key]
        },
        on: {
          click: () => {
            component[key](row)
          }
        }
      },
      actions[key]
    )
  })
  // return h(
  //   'el-button-group', {},
  //   btns
  // )
}

function changToOptions (data, name, id) {
  return data.reduce((result, current) => {
    let obj = {}
    obj['label'] = current[name]
    obj['value'] = current[id]
    result.push(obj)
    return result
  }, [])
}

function objToArrOptions (obj) {
  return Object.keys(obj).map(key => {
    return {
      label: obj[key],
      value: key
    }
  })
}

function evil (fn) {
  var Fn = Function // Function
  return new Fn('return ' + fn)()
}
function disposalField (fields, useType) {
  return fields.filter((item) => {
    if ('use' in item && item.use.length > 0) {
      const bool = item.use.some((num) => {
        return num === useType
      })
      if (bool) {
        return item
      }
    } else {
      return item
    }
  })
}
// header: Authorization
function authorization () {
  return 'Basic Y2xpZW50OmFkbWlu'
}

// 
function scrollToTop({ top = 0, left = 0, type = 'smooth', dom = 'page-code--child' } = {}) {
  const tableNode = document.getElementsByClassName(dom)[0]
  tableNode && tableNode.scrollTo({
    top,
    left,
    behavior: type
  })
}

export default { createOperationRender, changToOptions, objToArrOptions, evil, commonOperation, commonOperationEvent, disposalField, authorization, scrollToTop }
