// -
export function quickComList (list) {
  const arr = [
    {
      id: 0,
      text: this.$t('lower.lastWeek'),
      onClick (picker) {
        const end = new Date()
        const start = new Date()
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
        picker.$emit('pick', [start, end])
      }
    }, {
      id: 1,
      text: this.$t('lower.lastMon'),
      onClick (picker) {
        const end = new Date()
        const start = new Date()
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
        picker.$emit('pick', [start, end])
      }
    }, {
      id: 2,
      text: this.$t('lower.lastThreeMon'),
      onClick (picker) {
        const end = new Date()
        const start = new Date()
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
        picker.$emit('pick', [start, end])
      }
    }
  ]
  return list ? arr.filter(item => list.some(e => e === item.id)) : arr
}