export default {
  options (h, conf, key) {
    const list = []
    conf.__slot__.options.forEach((step, index) => {
      list.push(
        <el-step
          title={ step.title }
          description={step.description}
          icon={step.icon}
          status={step.status}
        ></el-step>
      )
    })
    return list
  }
}
