/**
 *  
 * @param {*} fileName
 */
export function matchFileType (fileName) {
  let suffix = '' // 
  let result = '' // 
  try {
    const fileList = fileName.split('.')
    suffix = fileList[fileList.length - 1]
  } catch (e) {
    suffix = ''
  }
  if (!suffix) {
    result = false
    return result
  }
  // txt
  const txtlist = ['txt']
  result = txtlist.some(function (item) {
    return item === suffix
  })
  if (result) {
    result = 'txt'
    return result
  }

  // txt
  const excelist = ['xls', 'xlsx']
  result = excelist.some(function (item) {
    return item === suffix
  })
  if (result) {
    result = 'excel'
    return result
  }

  // txt
  const wordlist = ['doc', 'docx']
  result = wordlist.some(function (item) {
    return item === suffix
  })
  if (result) {
    result = 'word'
    return result
  }

  //  pdf
  const pdflist = ['pdf']
  result = pdflist.some(function (item) {
    return item === suffix
  })
  if (result) {
    result = 'pdf'
    return result
  }
  //  ppt
  const pptlist = ['ppt']
  result = pptlist.some(function (item) {
    return item === suffix
  })
  if (result) {
    result = 'ppt'
    return result
  }

  result = 'other'

  return result
}

/**
 * 
 * @param {*} param0
 * @returns
 */
export function dynamicVar ({ option, row } = {}) {
  let result = option.confirmContent
  const _reg1 = /\$\{(.*?)\}/gm
  try {
    result = result.replace(_reg1, (m, k) => row[k])
  } catch {
    // eslint-disable-next-line no-template-curly-in-string
    console.warn('${xxx}')
  }
  return result
}
