<template>
  <div class="tree-wrapper">
    <template v-if="treeProp.filterNode">
      <el-input
        placeholder=""
        v-model="filterText"
        clearable
        class="filter-input"
      ></el-input>
    </template>
    <el-tree
      :data="data"
      :props="props"
      v-bind="treePropList"
      :ref="treeProp.id"
      :filter-node-method="filterNode"
      @check="checkChange"
    >
    </el-tree>
  </div>
</template>
<script>
import { createSandbox } from '@cseed/public-utils'
export default {
  inject: {
    baseIndex: {
      form: 'baseIndex',
      default: () => {
        return null
      }
    },
    formData: {
      form: 'formData',
      default: () => {
        return null
      }
    }
  },
  props: {
    data: {
      type: Array,
      default: () => []
    },
    props: {
      type: Object,
      default: () => {}
    },
    treeProp: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {
      filterText: ''
    }
  },
  computed: {
    treePropList () {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const _this = this
      const props = this.treeProp
      for (const prop in props) {
        // 
        // if (typeof props[prop] === 'string') {
        //   props[prop] = props[prop].replace(/^\s*/g, '')
        // }
        if (props[prop]) {
          try {
            // const renderFn = consts.evil(props[prop])
            if (prop === 'render') {
              props['render-content'] = (h, { node, data, store }) => {
                // return renderFn.call(_this, h, { node, data, store })
                return createSandbox.call(_this, { code: props[prop], h, node, data, store })
              }
            } else if (prop === 'checkedRender') {
              props.defaultCheckedKeys = this.formData
                // ? renderFn.call(_this, { data: _this.data, formData: this.formData() })
                // : renderFn.call(_this, { data: _this.data })
                ? createSandbox.call(_this, { code: props[prop], data: _this.data, formData: this.formData() })
                : createSandbox.call(_this, { code: props[prop], data: _this.data })
            } else if (prop === 'expandedRender') {
              props.defaultExpandedKeys = this.formData
                // ? renderFn.call(_this, { data: _this.data, formData: this.formData() })
                // : renderFn.call(_this, { data: _this.data })
                ? createSandbox.call(_this, { code: props[prop], data: _this.data, formData: this.formData() })
                : createSandbox.call(_this, { code: props[prop], data: _this.data })
            }
          } catch (err) {
            console.warn(err)
            this.$notify.error(this.$t('lower.ph71'))
          }
        }
      }
      return this.treeProp
    }
  },
  methods: {
    filterNode (value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    checkChange () {
      const list = this.treeProp.rtCheckedType === 'key'
        ? this.$refs[this.treeProp.id].getCheckedKeys()
        : this.$refs[this.treeProp.id].getCheckedNodes()
      this.$emit('treeCol', list)
    }
  },
  watch: {
    filterText (val) {
      this.$refs.advanceTree.filter(val)
    }
  }
}
</script>
<style lang="stylus" scoped>
.tree-wrapper
  .filter-input
    margin-bottom: 10px
</style>
