<template>
  <div class="page-code--child">
    <Page
      ref="lowerPage"
      v-if="lowerJson"
      v-show="isShowList"
      :mode="mode"
      :lower-json="lowerJson"
      :title="title"
      :key="refreshKey"
      :row="row"
      :_headers="_headers"
      :fetchMethod="fetchMethod"
      @action="handleAction"
      @option="handleOption"
      @todo="handleTodo"
      @selected="handleSelected"
      @go-back="val => $emit('go-back', val)"
    ></Page>
    <SubPage
      v-if="!isShowList"
      :lower-json="subLowerJson"
      :title="subTitle"
      :row="subRow"
      :mode="subMode"
      :template="template"
      :action="subAction"
      :menu-id="menuId"
      @go-back="goBack"
    ></SubPage>
    <!--  -->
    <el-dialog
      custom-class="lower-pop"
      :close-on-click-modal="false"
      :title="subTitle"
      :visible.sync="dialogVisible"
      :width="option.width || '60%'"
      :top="option.top || '15vh'"
    >
      <SubPage
        v-if="dialogVisible && (template || subLowerJson)"
        :lower-json="subLowerJson"
        :row="subRow"
        :mode="subMode"
        :template="template"
        :action="subAction"
        :menu-id="menuId"
        @go-back="hiddenDialog"
      ></SubPage>
    </el-dialog>
    <!--  -->
    <el-drawer
      custom-class="lower-pop"
      :wrapperClosable="false"
      :title="subTitle"
      :visible.sync="drawerVisible"
      :width="option.width || '40%'"
    >
      <SubPage
        v-if="drawerVisible && subLowerJson"
        :lower-json="subLowerJson"
        :row="subRow"
        :mode="subMode"
        :template="template"
        :action="subAction"
        :menu-id="menuId"
        @go-back="hiddenDrawer"
      ></SubPage>
    </el-drawer>
  </div>
</template>
<script>
import Page from './Page.vue'
import SubPage from './SubPage.vue'
import { createSandbox } from '@cseed/public-utils'
// import { dynamicVar } from '../utils/renderUtils'
export default {
  name: 'uiLowerEngine',
  props: {
    menuDetail: {
      type: [Object, String]
    },
    fetchMethod: {
      type: Function,
      default: () => this.fetch
    },
    menuDetailMethod: {
      type: Function,
      default: () => {
        return {}
      }
    },
    pageDetailMethod: {
      type: Function,
      default: () => {
        return {}
      }
    },
    handleResource: {
      type: Object,
      default: () => {
        return {
          pages: {},
          dialogs: {},
          drawers: {},
          todo: {},
          renderMethods: {}
        }
      }
    },
    _headers: {
      type: Function,
      default: () => {
        return {}
      }
    },
    json: {
      type: [Object, Number],
      default: null
    },
    title: {
      type: String
    },
    row: {
      type: [Object, Number]
    },
    mode: {
      type: String,
      default: 'edit'
    },
    action: {
      type: String,
      default: 'Page'
    },
    menuId: {
      type: Number
    }
  },
  provide () {
    return {
      baseIndex: this,
      // api
      setChildrenRef: (name, ref) => {
        this[name] = ref
      },
      // api
      getChildendRef: (name) => {
        return this[name]
      }
    }
  },
  components: {
    Page,
    SubPage
  },
  data () {
    return {
      isShowList: true,
      dialogVisible: false,
      drawerVisible: false, // 
      subRow: null,
      option: {},
      subTitle: '',
      subMode: 'edit',
      lowerJson: null,
      subLowerJson: null,
      refreshKey: '',
      selected: [], // 
      template: '', // 
      subAction: 'Page' // //
    }
  },
  created () {
    // todo.js
    this.injectTodoFn(this.handleResource.todo)
    // 
    this.initData()
  },
  methods: {
    injectTodoFn (todos) {
      // 
      const doubleMethods = Object.keys(todos).filter(todo => {
        return Object.keys(this).some(method => method === todo)
      })
      if (doubleMethods.length) {
        this.$notify.warning('todo:' + doubleMethods.join(',') + ', ')
      }
      Object.keys(todos).forEach(key => {
        this[key] = todos[key]
      })
    },
    async initData () {
      this.lowerJson = this.json ? this.json : await this.handleChidConf(this.menuDetail.pageConfigurationUniqueId)
    },
    /**
     *  
     * option 
     * row 
     */
    async handleAction (option, row = null) {
      const { label, action, pageMapId, mode } = option
      const localPageReg = /\$\{(.*?)\}/gm
      this.subTitle = label
      this.subMode = mode
      this.subRow = row
      this.subAction = action
      this.option = option
      this.clearLocalParams()
      // 
      if (localPageReg.test(pageMapId)) {
        this.template = pageMapId.replace(localPageReg, (m, k) => k)
        this.showActionType(action)
      } else {
        // 
        action !== 'Todo' && this.handleChidConf(pageMapId).then((res) => {
          this.subLowerJson = res
          this.showActionType(action)
        })
      }
    },
    // //
    showActionType (action) {
      if (action === 'Page') {
        this.isShowList = false
      } else if (action === 'Dialog') {
        this.dialogVisible = true
      } else if (action === 'Drawer') {
        this.drawerVisible = true
      }
    },
    //  
    clearLocalParams () {
      this.template = ''
    },
    /**
     *  
     */
    handleOption ({ option, row } = {}) {
      this.handleAction(option, row)
    },
    /**
     * 
     */
    handleTodo ({ option, row } = {}) {
      //  todo
      const localTodoReg = /^\$\{(.*?)\}$/gm
      if (localTodoReg.test(option.render)) {
        const fun = option.render.replace(localTodoReg, (m, k) => k)
        if (this[fun]) {
          this[fun]({ row, option })
        } else {
          this.$notify.error(this.$t('cMessage.missingMethods'))
        }
        return false
      }
      this.execRender({ option, row })
      // if (option.isComfirm) {
      //   // 
      //   const content = dynamicVar.call(this, { option, row })
      //   this.$confirm(`${content} ?`, '', {
      //     confirmButtonText: '',
      //     cancelButtonText: '',
      //     type: 'warning'
      //   }).then(() => {
      //     this.execRender({ option, row })
      //   }).catch(() => {
      //     this.$notify.info('')
      //   })
      // } else {
      //   this.execRender({ option, row })
      // }
    },
    /**
     * 
     */
    execRender ({ option, row } = {}) {
      try {
        if (option.render) {
          createSandbox.call(this, { code: option.render, curRow: row, curOption: option })
        }
      } catch (err) {
        console.warn(err)
        this.$notify.error(this.$t('lower.ph71'))
      }
    },
    /**
     * 
     */
    handleSelected (col) {
      this.selected = col
    },
    /**
     * ID
     */
    handleChidConf (uniqueId) {
      return new Promise(resolve => {
        this.pageDetailMethod({ uniqueId }).then(conf => {
          resolve(JSON.parse(conf.data.setting))
        })
      })
    },
    /**
     * 
     * isRefresh: true  false
     */
    goBack (isRefresh) {
      this.isShowList = true
      this.subTitle = ''
      this.subRow = ''
      this.refreshPage(isRefresh)
    },
    /**
     * 
     * isRefresh: true  false
     */
    hiddenDialog (isRefresh) {
      this.dialogVisible = false
      this.subRow = ''
      this.refreshPage(isRefresh)
      console.log(this)
    },
    /**
     * 
     * isRefresh: true  false
     */
    hiddenDrawer (isRefresh) {
      this.drawerVisible = false
      this.subRow = ''
      this.refreshPage(isRefresh)
    },
    // 
    refreshPage (isRefresh) {
      if (isRefresh === 'reload') {
        this.refreshKey = +new Date()
      }
      if (isRefresh) {
        this['cc-lower-table'].$parent.fetchData()
      }
    }
  },
  computed: {
    detailData () {
      return this.form
    }
  }
}
</script>
