import { getCurrentDate } from '../utils'
import dayjs from 'dayjs'
import cloneDeep from 'lodash/cloneDeep'

const componentChild = {}
/**
 * ./slotscomponentChild
 * keyJSON__config__.tag
 * valueJSON__slot__
 */
const slotsFiles = require.context('./slots', false, /\.js$/)
const keys = slotsFiles.keys() || []
keys.forEach(key => {
  const tag = key.replace(/^\.\/(.*)\.\w+$/, '$1')
  const value = slotsFiles(key).default
  componentChild[tag] = value
})

function vModel (dataObject, confClone) {
  const config = confClone.__config__
  if (config.tag === 'el-date-picker' && config.showCurrentTime && !config.defaultValue) {
    dataObject.props.value = getCurrentDate(confClone)
  } else {
    dataObject.props.value = config.defaultValue
  }

  dataObject.on.input = val => {
    this.$emit('input', val)
  }

  // 
  dataObject.on.treeCol = val => {
    this.$emit('treeEmit', val)
  }

  dataObject.on.action = val => {
    this.$emit('tableAction', val)
  }
  dataObject.on.option = val => {
    this.$emit('tableOption', val)
  }
  dataObject.on.todo = val => {
    this.$emit('tableTodo', val)
  }
  dataObject.on.tableSel = val => {
    this.$emit('tableSel', val)
  }

  if (config.tag === 'el-upload' && config.defaultValue) {
    setUploadDefaultValue.call(this, dataObject, config)
  }
}

// el-upoload
function setUploadDefaultValue (dataObject, { defaultValue }) {
  const list = typeof defaultValue === 'string' ? [defaultValue] : defaultValue
  dataObject.attrs['file-list'] = list.map(url => ({ name: `upload_${+new Date()}`, url }))
}

function mountSlotFiles (h, confClone, children) {
  const childObjs = componentChild[confClone.__config__.tag]
  if (childObjs) {
    Object.keys(childObjs).forEach(key => {
      const childFunc = childObjs[key]
      if (confClone.__slot__ && confClone.__slot__[key]) {
        children.push(childFunc.call(this, h, confClone, key))
      }
    })
  }
}

/**
 * 
 * @param {*} confClone
 */
function emitEvents (confClone) {
  ['on', 'nativeOn'].forEach(attr => {
    const eventList = confClone[attr] || []
    confClone[attr] = {}
    eventList.forEach(ele => {
      const { name, value } = ele
      confClone[attr][name] = event => this.$emit(name, { value, event })
    })
  })
}

/**
 * jsx - 
 * vue&jsx
 * @param {*} confClone
 * @param {*} dataObject
 */
function buildDataObject (confClone, dataObject) {
  Object.keys(confClone).forEach(key => {
    const keyVal = confClone[key]
    // vModel
    if (key === '__vModel__') {
      vModel.call(this, dataObject, confClone)
    } else if (key in dataObject) {
      if (Object.prototype.toString.call(keyVal) === '[object Object]') { // 
        dataObject[key] = { ...dataObject[key], ...keyVal }
      } else if (Object.prototype.toString.call(keyVal) === '[object Array]') { // 
        if (key === 'on' || key === 'nativeOn') {
          keyVal.forEach(event => {
            const { name, value } = event
            dataObject[key][name] = value
          })
        } else {
          dataObject[key] = [...keyVal]
        }
      } else if ( // 
        ['boolean', 'string', 'number'].includes(typeof dataObject[key])
      ) {
        dataObject[key] = keyVal
      }
    } else {
      // attrs
      if (key === 'placeholder') {
        dataObject.attrs[key] = keyVal || `${this.$t('')}${this.$t(confClone.__config__.label)}`
      } else if (key === 'picker-options') {
        // 
        dataPickerLimit.call(this, dataObject, key, keyVal, confClone)
      } else if (key === 'percentage' || key === 'src') {
        // percentage
        dataObject.attrs[key] = confClone.__config__.defaultValue || keyVal
      } else if (confClone.__config__.tag === 'el-link' && key === 'href') {
        if (keyVal) dataObject.attrs[key] = keyVal
      } else if (confClone.__config__.tag === 'el-transfer' && key === 'buttonTexts') {
        // 
        handleTransferWidth.call(this)
        dataObject.attrs[key] = keyVal
      } else if (confClone.__config__.tag === 'el-upload' && key === 'headers') {
        // 
        const httpReq = this.headers ? this.headers() : this.$headers ? this.$headers() : {}
        dataObject.attrs[key] = Object.assign(keyVal, httpReq)
      } else {
        dataObject.attrs[key] = keyVal
      }
    }
  })

  // 
  clearAttrs(dataObject, confClone)
}

function clearAttrs (dataObject, confClone) {
  if (!confClone.__config__.isStandData) {
    delete dataObject.attrs.dynamicConfig
    delete dataObject.attrs.__config__
  }
  delete dataObject.attrs.__slot__
  delete dataObject.attrs.__methods__
  delete dataObject.attrs.__senior__
}

function dataPickerLimit (dataObject, key, keyVal, itemConfig) {
  let limit
  if (keyVal === 1) {
    // 
    limit = {
      disabledDate: (time) => {
        return time.getTime() < Date.now() - 8.64e7
      }
    }
    const chooseTime = dayjs(itemConfig.__config__.defaultValue).format('YYYY-MM-DD')
    const nowDate = dayjs().format('YYYY-MM-DD')
    if (chooseTime === nowDate) {
      limit.selectableRange = `${dayjs().format('HH:mm:ss')} - 23:59:59`
    } else {
      limit.selectableRange = '00:00:00 - 23:59:59'
    }
  } else if (keyVal === 2) { // 
    limit = {
      disabledDate: (time) => {
        return time.getTime() > Date.now()
      }
    }
  } else {
    limit = {
      disabledDate: () => {
        return null
      }
    }
  }
  dataObject.attrs[key] = limit
}

/**
 * 
 * @param {*}
 */
function handleTransferWidth () {
  this.$nextTick(() => {
    const allBnttonNodes = document.querySelectorAll('.el-transfer__button')
    const allTransferNodes = document.querySelectorAll('.el-transfer-panel')
    for (let i = 0; i < allTransferNodes.length; i++) {
      const btnWidth = allBnttonNodes[i].offsetWidth || 60
      const queryWidth = `calc(50% - ${btnWidth + 40}px)`
      allTransferNodes[i].style.width = queryWidth
    }
  })
}

function makeDataObject () {
  return {
    class: {},
    attrs: {},
    props: {},
    domProps: {},
    nativeOn: {},
    on: {},
    style: {},
    directives: [],
    scopedSlots: {},
    slot: null,
    key: null,
    ref: null,
    refInFor: true
  }
}

export default {
  props: {
    conf: {
      type: Object,
      required: true
    },
    headers: {
      type: Function
    }
  },
  inject: {
    // 
    setChildrenRef: {
      default: () => {}
    }
  },
  render (h) {
    const dataObject = makeDataObject()
    const confClone = cloneDeep(this.conf)
    const children = this.$slots.default || []

    // slotstag
    mountSlotFiles.call(this, h, confClone, children)

    // 
    emitEvents.call(this, confClone)

    // jsonvue render dataObject
    buildDataObject.call(this, confClone, dataObject)

    return h(this.conf.__config__.tag, dataObject, children)
  }
}
