export default {
  options (h, conf, key) {
    const list = []
    conf.__slot__.options.forEach((tab, index) => {
      list.push(
        <el-tab-pane
          disabled={tab.disabled}
          name={tab.name}
          closable={tab.closable}
        >
          <template slot="label"><i class={ tab.icon } style="margin-right: 5px"></i>{ tab.label }</template>
        </el-tab-pane>
      )
    })
    return list
  }
}
