import get from 'lodash/get'
import { advanceForm } from '@cseed/public-utils'
/**
 * render 
 */
export default {
  methods: {
    setExtraAttr ({ formModel, formRef, fields }) {
      fields.forEach(field => {
        // 
        this.setSlotList({ field, slot: field.__slot__ })
        this.setUploadConfig({
          field,
          config: field.__config__,
          vModel: field.__vModel__,
          slot: field.__slot__,
          formModel,
          formRef
        })
        if (field.__config__.children) {
          this.setExtraAttr({ formModel, formRef, fields: field.__config__.children })
        }
      })
    },
    // 
    setSlotList ({ field, slot }) {
      slot && slot.slotName && this.slotCol.push(slot.slotName)
    },
    /**
     * upload
     */
    setUploadConfig ({ field, config, vModel, formModel, formRef } = {}) {
      if (config.tag === 'el-upload') {
        field['on-success'] = function (res, file, fileList) {
          // 
          if (res.success || res.code === 1000 || res.code === '1000' || res.code === 200) {
            const prev = this[formModel][vModel] || []
            this.$set(this[formModel], vModel, [
              ...prev,
              get(res, config.attrMap)
            ])
            this.$refs[formRef] && this.$refs[formRef].validateField(vModel)
          } else { // 
            this.$notify({
              type: 'warning',
              message: res.msg || ''
            })
            const index = fileList.findIndex(v => v.uid === file.uid)
            fileList.splice(index, 1)
          }
        }
        field['on-remove'] = function (file, fileList) {
          this.$set(
            this[formModel],
            vModel,
            fileList.map(f => (f.response ? f.response.data : f.url))
          )
          this.$refs[formRef] && this.$refs[formRef].validateField(vModel)
        }
        field['before-upload'] = function (file) {
          let isRightSize = true
          let isAccept = true
          if (field.accept) {
            isAccept = new RegExp(`${field.accept}`).test(file.type)
            if (!isAccept) {
              setTimeout(() => {
                this.$notify({
                  message: `${field.accept}`,
                  type: 'warning'
                })
              })
            }
          }
          if (config.fileSize) {
            const units = {
              KB: 1024,
              MB: 1024 * 1024,
              GB: 1024 * 1024 * 1024
            }
            isRightSize = file.size / units[config.sizeUnit] < config.fileSize
            if (!isRightSize) {
              setTimeout(() => {
                this.$notify({
                  message: ` ${config.fileSize}${config.sizeUnit}`,
                  type: 'warning'
                })
              })
            }
          }
          return isRightSize && isAccept
        }
        if (!this.id) this.key = +new Date()
      }
    },
    // 
    setFillFormData ({ fields, formModel, formRef, formData } = {}) {
      fields.forEach((item, index) => {
        const config = item.__config__
        // eslint-disable-next-line no-unused-vars
        const senior = item.__senior__ || {}
        const slot = item.__slot__ || {}
        const value = formData[item.__vModel__]
        // 
        this.setSlotList({ field: item, slot })
        // 
        if (config.tag === 'el-upload') {
          let val = []
          if (formData[item.__vModel__] && typeof formData[item.__vModel__] === 'string') {
            val = formData[item.__vModel__].split(',')
          }
          item['file-list'] = val.map(url => ({ name: `${item.__vModel__}${index}`, url }))
          this.setUploadConfig({
            field: item,
            config,
            vModel: item.__vModel__,
            formModel,
            formRef
          })
        }
        // 
        if (config.tag === 'el-descriptions') {
          slot.options && slot.options.forEach(desc => {
            if (desc.key in formData) {
              desc.value = formData[desc.key]
            }
          })
        }
        // 
        if (config.tag === 'el-image') {
          if (Array.isArray(formData[item.__vModel__])) {
            item['preview-src-list'] = formData[item.__vModel__]
          } else {
            item['preview-src-list'] = [formData[item.__vModel__] || item.src]
          }
        }
        // fix null/undefined
        if (item.__vModel__ in formData && advanceForm.isTrusData(value)) {
          this.$set(config, 'defaultValue', value)
        }
        if (config.children) {
          this.setFillFormData({
            fields: config.children,
            formModel,
            formRef,
            formData
          })
        }
      })
    }
  }
}
