import { dynamicVar } from '../utils/index'
/**
 * 
 * @param {*} arr 
 * @param {*} isDesign 
 * @param {*} props 
 * @returns
 */
export function commonOperation (arr, isDesign, operate) {
  return (h, { row }) => {
    const { fold, foldName = '', foldNum } = operate
    const sliceNum = foldNum || 1
    if (fold) {
      // 
      const excHideBtns = arr.filter(btn => {
        const rule = btn.rule || {}
        // eslint-disable-next-line no-eval
        const ruleExec = eval(rule.condition)
        return !(rule.type === 1 && ruleExec)
      })
      const showBtns = excHideBtns.slice(0, sliceNum)
      // 
      const hideBtns = arr.filter(element => showBtns.every(e => e.label !== element.label))
      return h(
        'div',
        {
          key: `cbt_${+new Date()}`
        },
        [
          showBtns.map((item, index) => renderBtn.call(this, { isDesign, row, h, item, index })),
          renderHoldBtns.call(this, { arr: hideBtns, isDesign, row, h, foldName })
        ]
      )
    } else {
      return h(
        'div',
        {
          key: `cbt_${+new Date()}`
        },
        arr.map((item, index) => renderBtn.call(this, { isDesign, row, h, item, index }))
      )
    }
  }
}

function renderBtn ({ isDesign, row, h, item, index } = {}) {
  const { label, action, powerCode, icon, type, size, rule = {}, isComfirm = false } = item
  // eslint-disable-next-line no-eval
  const ruleExec = eval(rule.condition)
  if (rule.type === 1 && ruleExec) {
    return ''
  }
  // 
  if (isComfirm) {
    const content = dynamicVar.call(this, { option: item, row }) // 
    return h(
      'el-popconfirm',
      {
        attrs: {
          title: content
        },
        style: {
          'margin-left': '10px'
        },
        directives: [{
          name: 'permission',
          value: isDesign ? null : powerCode
        }],
        scopedSlots: {
          reference: () => {
            return h(
              'el-button', {
                props: {
                  type,
                  size,
                  icon,
                  disabled: Boolean(ruleExec)
                }
              },
              label
            )
          }
        },
        on: {
          confirm: () => {
            emitEvent.call(this, { action, row, item })
          }
        }
      }
    )
  } else {
    return h(
      'el-button', {
        key: +new Date() + index,
        props: {
          type,
          size,
          icon,
          disabled: Boolean(ruleExec)
        },
        directives: [{
          name: 'permission',
          value: isDesign ? null : powerCode
        }],
        on: {
          click: () => {
            emitEvent.call(this, { action, row, item })
          }
        }
      },
      label
    )
  }
}

// 
function emitEvent ({ action, row, item }) {
  // 
  if (action === 'Todo') {
    this.$emit('todo', { row, option: item })
  } else if (action === 'Link' && item.linkConf.url) {
    window.open(item.linkConf.url, item.linkConf.target ? '_blank' : '')
  } else {
    this.$emit('option', {
      row,
      option: item
    })
  }
}

function renderHoldBtns ({ arr, isDesign, row, h, foldName } = {}) {
  return h('el-dropdown', {}, [
    h(
      'el-button',
      {
        props: {
          type: 'text',
          size: 'mini'
        },
        style: {
          marginLeft: '10px'
        }
      },
      [
        h(
          'span',
          {},
          foldName
        ),
        h('i', { class: 'el-icon-arrow-down el-icon--right' })
      ]
    ),
    h(
      'el-dropdown-menu',
      {
        slot: 'dropdown'
      },
      arr.map((item, index) => {
        return h(
          'el-dropdown-item',
          [
            renderBtn.call(this, { isDesign, row, h, item, index })
          ]
        )
      })
    )
  ])
}
