<template>
  <div class="tinymce-container">
    <textarea :id="tinymceId" class="tinymce-textarea"></textarea>
    <div class="editor-custom-btn-container" v-show="!$attrs.readonly">
      <editorImage
        :upload="$attrs.upload"
        color="#1890ff"
        class="editor-upload-btn"
        @successCBK="imageSuccessCBK"
      />
    </div>
  </div>
</template>
<script>
import editorImage from './EditorImage'
import loadTinymce from './loadTinymce'
import { plugins, toolbar, fontFormats, fontsizeFormats } from './config'
import { matchFileType } from '../utils/index'

let num = 1

export default {
  name: 'Tinymce',
  props: {
    id: {
      type: String,
      default: () => {
        num === 10000 && (num = 1)
        return `tinymce${+new Date()}${num++}`
      }
    },
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      hasChange: false,
      hasInit: false,
      tinymceId: this.id,
      languageTypeList: {
        'en': 'en',
        'zh_CN': 'zh_CN'
      }
    }
  },
  mounted () {
    loadTinymce(tinymce => {
      require('./zh_CN')
      let conf = {
        selector: `#${this.tinymceId}`,
        language: 'zh_CN',
        menubar: 'file edit insert view format table',
        plugins,
        toolbar,
        height: 450, // 
        // min_height: 400,
        readonly: false,
        branding: false,
        placeholder: '',
        // resize: false,
        // object_resizing: false,
        end_container_on_empty_block: true,
        powerpaste_word_import: 'clean',
        advlist_bullet_styles: 'square',
        advlist_number_styles: 'default',
        default_link_target: '_blank',
        link_title: false,
        nonbreaking_force_tab: true,
        font_formats: fontFormats,
        fontsize_formats: fontsizeFormats
      }
      conf = Object.assign(conf, this.$attrs)
      conf.init_instance_callback = editor => {
        if (this.value) editor.setContent(this.value)
        this.vModel(editor)
      }
      tinymce.init(conf)
    })
  },
  destroyed () {
    this.destroyTinymce()
  },
  methods: {
    destroyTinymce () {
      if (!window.tinymce) return
      const tinymce = window.tinymce.get(this.tinymceId)
      if (tinymce) {
        tinymce.destroy()
      }
    },
    vModel (editor) {
      this.$watch('value', (val, preVal) => {
        if (editor && val !== preVal && val !== editor.getContent()) {
          if (typeof val !== 'string') val = val.toString()
          editor.setContent(val)
        }
      })
      editor.on('change keyup undo redo, SetContent', () => {
        this.hasChange = true
        this.$emit('input', editor.getContent())
      })
    },
    imageSuccessCBK (arr) {
      arr.forEach(item => {
        let html = ''
        if (item.type === 'image') {
          html = `<img class="wscnph" src="${item.url}" >`
        } else if (item.type === 'video') {
          html = `<video width="400" src="${item.url}" controls></video>`
        } else {
          const icon = matchFileType(item.name)
          html = `<p>
            <a
              style="cursor: pointer;text-decoration: none; font-size: 14px;"
              href="${item.url}"
              download
              rel="noopener"
            ><img src="${require('./img/' + icon + '.png')}" /> ${item.name}</a></p>`
        }
        return window.tinymce.get(this.tinymceId).insertContent(html)
      })
    }
  },
  components: {
    editorImage
  }
}
</script>
<style lang="stylus" scoped>
.tinymce-container
  position relative
  line-height normal
  ::v-deep
    .mce-fullscreen
      z-index: 10000
  .tinymce-textarea
    visibility hidden
    z-index -1
  .editor-custom-btn-container
    position absolute
    right 4px
    top 4px
    z-index 1
  .fullscreen .editor-custom-btn-container
    z-index 10000
    position fixed
  .editor-upload-btn
    display: inline-block
</style>
