import Vue from 'vue'
import LowcodeEngine from './views/Index.vue'
import Parser from './parser/Index.vue' // 
import render from './render/index' //  render
import './style/index.styl'
import ref from 'vue-ref' // 

const lowerComponents = require('@cseed/ui-lower-components') // 

const install = function (Vue, options = {}) {
  if (!options.fetch) console.error('no fetch')
}

Vue.use(lowerComponents)
Vue.use(ref, { name: 'lower-ref' })

export {
  install,
  LowcodeEngine,
  Parser,
  render
}
export default {
  install,
  LowcodeEngine,
  Parser,
  render
}
