<template>
  <div style="width:60%;margin:20px auto">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="120px"
      class="demo-ruleForm"
    >
      <div>
        <el-form-item label="" prop="skillName">
          <el-input v-model="ruleForm.skillName"
                    maxlength="50"
                    show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="appPkg">
          <el-input v-model="ruleForm.appPkg"
                    maxlength="50"
                    show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="appVer">
          <el-input v-model="ruleForm.appVer"
                    maxlength="10"
                    show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="startAction">
          <el-select v-model="ruleForm.startAction" placeholder="">
            <el-option label="activity" :value="100001"></el-option>
            <el-option label="broadcast" :value="100002"></el-option>
            <el-option label="service" :value="100003"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="startMethod">
          <el-select v-model="ruleForm.startMethod" placeholder="">
            <el-option label="uri" :value="200001"></el-option>
            <el-option label="action" :value="200002"></el-option>
            <el-option label="className" :value="200003"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="startParam">
          <el-input v-model="ruleForm.startParam"
                    maxlength="200"
                    show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="extendParam">
          <div v-for="(item,index) in ruleForm.extendParam" :key="index+'item'" style="margin:5px 0">
            <el-input v-model="item.key" placeholder="key" style="width:calc(50% - 5px);margin-right:10px"
                      maxlength="50"
                      show-word-limit
            ></el-input>
            <el-input v-model="item.value" placeholder="value" style="width:calc(50% - 5px)"
                      maxlength="50"
                      show-word-limit
            ></el-input>
          </div>
          <div class="addextendParamMapping" @click="addextendParam(ruleForm.extendParam)">
            + 
          </div>
        </el-form-item>
        <el-form-item label="" prop="extendParamMapping">
          <div v-for="(item,index) in ruleForm.extendParamMapping" :key="index+'item'" style="margin:5px 0">
            <el-input v-model="item.skillKey"
                      maxlength="50"
                      show-word-limit placeholder="key" style="width:calc(50% - 25px);"
            ></el-input>
            <div style="display:inline-block;width:50px;text-align:center"></div>
            <el-input v-model="item.eventKey"
                      maxlength="50"
                      show-word-limit placeholder="key" style="width:calc(50% - 25px)"
            ></el-input>
          </div>
          <div class="addextendParamMapping" @click="addextendParamMapping(ruleForm.extendParamMapping)">
            + 
          </div>
        </el-form-item>
        <el-form-item label="" prop="skillDesc">
          <el-input type="textarea" v-model="ruleForm.skillDesc"
                    maxlength="200"
                    show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="isErrorAction">
          <el-radio-group v-model="ruleForm.isErrorAction" @input="addErrorActionInfo">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
          </el-radio-group>
        </el-form-item>
        <div v-if="ruleForm.isErrorAction">
          <el-form
            :model="ruleForm.errorActionInfo"
            :rules="rules"
            ref="ruleForm2"
            label-width="120px"
            class="demo-ruleForm"
          >
            <el-form-item label="" prop="appPkg">
              <el-input v-model="ruleForm.errorActionInfo.appPkg"
                        maxlength="50"
                        show-word-limit
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="appVer">
              <el-input v-model="ruleForm.errorActionInfo.appVer"
                        maxlength="10"
                        show-word-limit
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="startAction">
              <el-select v-model="ruleForm.errorActionInfo.startAction" placeholder="">
                <el-option label="activity" :value="100001"></el-option>
                <el-option label="broadcast" :value="100002"></el-option>
                <el-option label="service" :value="100003"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="startMethod">
              <el-select v-model="ruleForm.errorActionInfo.startMethod" placeholder="">
                <el-option label="uri" :value="200001"></el-option>
                <el-option label="action" :value="200002"></el-option>
                <el-option label="className" :value="200003"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="startParam">
              <el-input v-model="ruleForm.errorActionInfo.startParam"
                        maxlength="200"
                        show-word-limit
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="extendParam">
              <div v-for="(item,index) in ruleForm.errorActionInfo.extendParam" :key="index+'item'" style="margin:5px 0">
                <el-input v-model="item.key" placeholder="key"
                          maxlength="50"
                          show-word-limit style="width:calc(50% - 5px);margin-right:10px"
                ></el-input>
                <el-input v-model="item.value" placeholder="value"
                          maxlength="50"
                          show-word-limit style="width:calc(50% - 5px)"
                ></el-input>
              </div>
              <div class="addextendParamMapping" @click="addextendParam(ruleForm.errorActionInfo.extendParam)">
                + 
              </div>
            </el-form-item>
            <el-form-item label="" prop="extendParamMapping">
              <div v-for="(item,index) in ruleForm.errorActionInfo.extendParamMapping" :key="index+'item'" style="margin:5px 0">
                <el-input v-model="item.skillKey"
                          maxlength="50"
                          show-word-limit placeholder="key" style="width:calc(50% - 25px);"
                ></el-input>
                <div style="display:inline-block;width:50px;text-align:center"></div>
                <el-input v-model="item.eventKey"
                          maxlength="50"
                          show-word-limit placeholder="key" style="width:calc(50% - 25px)"
                ></el-input>
              </div>
              <div class="addextendParamMapping" @click="addextendParamMapping(ruleForm.errorActionInfo.extendParamMapping)">
                + 
              </div>
            </el-form-item>
          </el-form>
        </div>
      </div>

      <el-form-item>
        <el-button type="primary" @click="submitForm"></el-button>
        <el-button @click="$emit('go-back')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { ApiRequest } from '@/framework/services/CustomApi'
export default {
  props: {
    row: {
      type: Object,
      default () {
        return {}
      }
    },
    mode: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      ruleForm: {
        skillName: '',
        extendParam: [
          {
            key: '',
            value: ''
          }
        ],
        extendParamMapping: [{
          eventKey: '',
          skillKey: ''
        }],
        isErrorAction: 0
      },
      rules: {
        skillName: [
          { required: true, message: '' },
          { required: true, message: '' }
        ],
        appPkg: [
          { required: true, message: '' }
        ],
        appVer: [
          { required: true, message: '' },
          { pattern: /^[0-9]*$/, message: '', trigger: 'blur' }
        ],
        startAction: [
          { required: true, message: '' }
        ],
        startMethod: [
          { required: true, message: '' }
        ],
        startParam: [
          { required: true, message: '' }
        ]
      }
    }
  },
  methods: {
    addErrorActionInfo (flag) {
      if (flag === 1) {
        this.$set(this.ruleForm, 'errorActionInfo', {
          extendParam: [
            {
              key: '',
              value: ''
            }
          ],
          extendParamMapping: [{
            eventKey: '',
            skillKey: ''
          }]
        })
      } else {
        this.$set(this.ruleForm, 'errorActionInfo', {})
      }
    },
    addextendParam (data) {
      data.push({
        key: '',
        value: ''
      })
    },
    addextendParamMapping (data) {
      data.push({
        eventKey: '',
        skillKey: ''
      })
    },
    submitForm () {
      this.$refs.ruleForm.validate((valid) => {
        if (this.ruleForm.isErrorAction) {
          this.$refs.ruleForm2.validate((valid2) => {
            if (valid && valid2) {
              ApiRequest(this.mode === 'edit' ? 'updateSkill' : 'addSkill', this.ruleForm).then(res => {
                this.$emit('go-back', true)
                this.$message({
                  type: 'success',
                  message: ''
                })
              })
            } else {
              return false
            }
          })
        } else {
          if (valid) {
            ApiRequest(this.mode === 'edit' ? 'updateSkill' : 'addSkill', this.ruleForm).then(res => {
              this.$emit('go-back', true)
              this.$message({
                type: 'success',
                message: ''
              })
            })
          } else {
            return false
          }
        }
      })
    }
  },
  created () {
    debugger
    if (this.mode === 'edit') {
      ApiRequest('getSkill', { id: this.row.id }).then(data => {
        this.ruleForm = data
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
.addextendParamMapping
  border: 1px dashed #ccc
  border-radius: 5px
  text-align center
  margin-top 10px
</style>
