<template>
  <div>
    <template v-for="(item, index) in items">
      <el-submenu
        v-if="item.children && item.children.length > 0"
        :key="item.route"
        :index="`${indexKey}_${index}`"
      >
        <template slot="title">
          <i v-if="item.icon" :class="item.icon"></i>
          <span>{{ $t(item.title) }}</span>
        </template>
        <!--  -->
        <menu-item :items="item.children" :index-key="`${indexKey}_${index}`"></menu-item>
      </el-submenu>
      <el-menu-item v-else :key="item.route" :index="item.route">
        <template v-if="isRightClick">
          <i v-if="item.icon" :class="item.icon"></i>
          <span slot="title">
            <router-link
              onclick="return false;"
              class="route-link"
              :to="{name: item.route}"
              v-if="!collapse"
            >
              {{ $t(item.title) }}
            </router-link>
            <template v-else>
              {{ $t(item.title) }}
            </template>
          </span>
          <!-- <span v-else>{{ $t(item.title) }}</span> -->
        </template>
        <template v-else>
          <i v-if="item.icon" :class="item.icon"></i>
          <span slot="title">{{ $t(item.title) }}</span>
        </template>
      </el-menu-item>
    </template>
  </div>
</template>

<script>
export default {
  name: 'MenuItem',
  props: {
    indexKey: {
      type: [String, Number],
      default: 1
    },
    items: {
      type: Array,
      default: () => {
        return []
      }
    },
    isRightClick: {
      type: Boolean,
      default: true
    },
    collapse: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="stylus" scoped>
a.route-link
  color inherit
  text-decoration none
</style>
