
import qs from 'qs'
import axios from 'axios'
import store from '@/store'
import router from '@/router'
import NProgress from 'nprogress'
import { Loading, Message } from 'element-ui'
import cseedLocale from '@cseed/locale'
import 'nprogress/nprogress.css'

import constants from './consts'
let loadingInstance
let logOutNum = 0

const instance = axios.create({
  timeout: 50000
})

export default function fetch ({
  method = 'get',
  url,
  data,
  params,
  options = {},
  headers = {},
  isJSON = true,
  emptyToken = false, // token
  loadingShow = true,
  isFile = false
}) {
  NProgress.start()
  if (!loadingInstance && loadingShow) {
    loadingInstance = Loading.service({ target: document.querySelector('.el-main') })
  }
  let pushData = null
  if (isJSON || data instanceof FormData) {
    pushData = data
  } else {
    if (typeof data === 'string') {
      pushData = data
    } else {
      pushData = qs.stringify(data)
    }
  }

  const option = {
    method,
    url: `/violet-api/${url}`,
    data: pushData,
    params,
    ...options
  }
  let token = ''
  if (emptyToken) {
    token = ''
  } else {
    const user = store.state.user
    if (user) {
      token = user.token
    }
  }
  const { productId: ProductId } = router.currentRoute.meta || {}
  option.headers = {
    AccessToken: token || '',
    Authorization: constants.authorization(),
    ProductId,
    ...headers
  }
  return instance(option)
    .then(function (res) {
      NProgress.done()
      if (isFile) {
        return res
      }
      const { data } = res
      if (data.success || data.code === 1000 || data.code === '1000' || data.code === 200) {
        // return data.data || data
        return data
      } else {
        // eslint-disable-next-line no-throw-literal
        throw {
          code: data.code,
          message: data.msg
        }
      }
    })
    .catch(e => {
      NProgress.done()
      if (e instanceof Error) {
        e = {
          code: e.name,
          message: e
        }
      }
      if (e.response) {
        const { data, status, statusText } = e.response
        e = {
          code: data.code + '' || status,
          message: data.msg || statusText || cseedLocale.$t('cMessage.fetchErr')
        }
      }
      // 
      if (e.code === '1998' || e.code === '2005') {
        if (logOutNum > 0) return
        logOutNum++
        Message({
          type: 'warning',
          message: cseedLocale.$t('cMessage.loginTimeOut')
        })
        setTimeout(function () {
          logOutNum = 0
          option.headers = {
            Authorization: ''
          }
          throw e
        }, 2000)
      } else {
        throw e
      }
    })
    .finally(() => {
      if (loadingInstance && loadingShow) {
        loadingInstance.close()
      }
      loadingInstance = ''
    })
}
