<template>
  <div style="position: relative">
    <el-input
      type="text"
      placeholder=""
      :id="searchId"
      v-model="keywords"
      @change="search"
      class="map-search"
    ></el-input>
    <div :id="mapId" :style="{ width: '100%', height: `${height}px` }"></div>
  </div>
</template>
<script>
import MP from './mapLoadScript'
// 
const mapAk = 'okFNZHPQ0BfF9Qhq3K1WmrRv'
let num = 1
export default {
  name: 'BaiduMap',
  props: {
    id: {
      type: String,
      default: () => {
        num === 10000 && (num = 1)
        return `map${+new Date()}${num++}`
      }
    },
    value: {
      type: Array,
      default () {
        return [114.06455184, 22.54845664]
      }
    }
  },
  data () {
    return {
      mapId: this.id,
      map: null,
      height: 300,
      searchId: `search${+new Date()}`,
      keywords: '',
      local: ''
    }
  },
  mounted () {
    this.init()
  },
  activated () {
    if (window.loadedMap) {
      this.initMap()
    }
  },
  methods: {
    init () {
      this.$nextTick(() => {
        MP(mapAk, (err) => {
          if (err) {
            this.$message.error('')
            return
          }
          this.initMap()
        })
      })
    },
    initMap () {
      const attrs = this.$attrs
      this.height = attrs.height
      // eslint-disable-next-line no-undef
      let mapData = new BMapGL.Map(this.mapId)
      // eslint-disable-next-line no-undef
      let point = new BMapGL.Point(
        parseFloat(this.value[0]),
        parseFloat(this.value[1])
      )
      // 
      mapData.centerAndZoom(point, attrs.zoom)
      // 
      if (attrs.enableScrollWheelZoom) {
        mapData.enableScrollWheelZoom()
      }
      // 
      if (!attrs.disableDoubleClickZoom) {
        mapData.disableDoubleClickZoom()
      }
      // 
      if (!attrs.disableDragging) {
        mapData.disableDragging()
      }
      // 
      if (attrs.enableContinuousZoom) {
        mapData.enableContinuousZoom()
      }
      // 
      if (attrs.scaleControl) {
        // eslint-disable-next-line no-undef
        mapData.addControl(new BMapGL.ScaleControl())
        // eslint-disable-next-line no-undef
        mapData.addControl(new BMapGL.ZoomControl())
      }
      // 3D
      if (attrs.navigationControl3D) {
        // eslint-disable-next-line no-undef
        mapData.addControl(new BMapGL.NavigationControl3D())
      }
      mapData.enableKeyboard()
      this.map = mapData
      // 
      // eslint-disable-next-line no-undef
      this.local = new BMapGL.LocalSearch(this.map, {
        renderOptions: { map: this.map }
      })
      // 
      this.vModel()
    },
    search () {
      this.local.search(this.keywords)
      // 
      // eslint-disable-next-line no-undef
      let gc = new BMapGL.Geocoder()
      gc.getPoint(this.keywords, (rs) => {
        if (rs) {
          const point = [rs.lng, rs.lat]
          this.$emit('input', point)
        }
      }, this.keywords)
    },
    vModel () {
      this.map.addEventListener('click', (e) => {
        const point = [e.latlng.lng, e.latlng.lat]
        this.$emit('input', point)
        // eslint-disable-next-line no-undef
        // let gc = new BMapGL.Geocoder()
        // eslint-disable-next-line no-undef
        // gc.getLocation(new BMapGL.Point(e.latlng.lng, e.latlng.lat), (rs) => {
        //   let addComp = rs.addressComponents
        //   this.$emit('input', `${addComp.province}-${addComp.city}-${addComp.district}`)
        // })
      }, false)
    }
  },
  watch: {
    $attrs () {
      this.initMap()
    }
  }
}
</script>
<style lang="stylus" scoped>
  .map-search
    width 300px
    position: absolute
    top: 10px
    left: 10px
    z-index 10
</style>
