<!--  -->
<template>
  <el-card class="box-card" :shadow="cardProps.shadow">
    <div slot="header" v-if="cardProps.showHeader">
      <div class="title">
        <span>
          {{ $t(cardProps.title) }}
          <span class="explainText" v-if="cardProps.explainText">
            <el-tooltip placement="top-start">
              <span slot="content" v-html="$t(cardProps.explainText)"></span>
              <i class="el-icon-question"></i>
            </el-tooltip>
          </span>
        </span>
        <!--  -->
        <div class="btn-group">
          <el-button-group>
            <el-button
              v-for="(btn, index) of btnGroup"
              :key="index"
              :type="cardProps.btnType"
              :icon="btn.icon"
              @click.stop="defineBtnClick(btn)"
            >{{ $t(btn.label) }}</el-button>
          </el-button-group>
          <el-button
            class="btnExpend"
            v-if="cardProps.showExpendBtn"
            :type="cardProps.btnType"
            @click.stop="expend"
          >
            <template v-if="!showExpend">{{ $t('lower.expand') }} <i class="el-icon-caret-bottom"></i></template>
            <template v-else>{{ $t('lower.stow') }} <i class="el-icon-caret-top"></i></template>
          </el-button>
        </div>
      </div>
      <div class="subtitle" v-if="cardProps.subTitle">{{ $t(cardProps.subTitle) }}</div>
    </div>
    <template v-if="showExpend">
      <slot></slot>
    </template>
  </el-card>
</template>
<script>
import { createSandbox } from '@cseed/public-utils'
export default {
  name: 'advanceCard',
  inject: {
    baseIndex: {
      form: 'baseIndex',
      default: () => {
        return null
      }
    }
  },
  props: {
    cardProps: {
      type: Object,
      default () {
        return {}
      }
    },
    btnGroup: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      showExpend: true
    }
  },
  methods: {
    expend () {
      this.showExpend = !this.showExpend
    },
    defineBtnClick (btn) {
      const _this = this.baseIndex || this
      try {
        if (btn.render) {
          createSandbox.call(_this, { code: btn.render })
        }
      } catch (err) {
        console.warn(err)
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
.title
  display flex
  justify-content space-between
  align-items center
  line-height 36px
.subtitle
  font-size 12px
  color #aaa
.el-button-group
  & > .el-button--text
    padding-right 5px
.btnExpend
  margin-left 5px
</style>
