<template>
  <div class="sub-page">
    <Index
      v-if="!template"
      :page-detail-method="baseIndex.pageDetailMethod"
      :menu-detail-method="baseIndex.menuDetailMethod"
      :fetch-method="baseIndex.fetchMethod"
      :handle-resource="baseIndex.handleResource"
      :_headers="baseIndex._headers"
      :json="lowerJson"
      :title="title"
      :row="row"
      :mode="mode"
      @go-back="val => $emit('go-back', val)"
    ></Index>
    <div v-else>
      <!--  -->
      <c-card v-if="action === 'Page'" :title="title" @go-back="$emit('go-back')">
        <component
          :is="tempSource[`${template}${action}`]"
          :mode="mode"
          :row="row"
          :title="title"
          :menu-id="menuId"
          @go-back="val => $emit('go-back', val)"
        />
      </c-card>
      <!--  -->
      <component
        v-if="action === 'Dialog' || action === 'Drawer'"
        :is="tempSource[`${template}${action}`]"
        :mode="mode"
        :row="row"
        :title="title"
        :menu-id="menuId"
        @go-back="val => $emit('go-back', val)"
      />
    </div>
  </div>
</template>
<script>
import { CCard } from '@cseed/admin-toolkit'
const Index = () => import('./Index.vue')
export default {
  inject: ['baseIndex'],
  props: [
    'lowerJson',
    'title',
    'todoFn',
    'row',
    'mode',
    'template',
    'action',
    'menuId'
  ],
  computed: {
    tempSource () {
      return this.action === 'Page'
        ? this.baseIndex.handleResource.pages
        : this.action === 'Dialog'
          ? this.baseIndex.handleResource.dialogs
          : this.baseIndex.handleResource.drawers
    }
  },
  components: {
    Index,
    CCard
  }
}
</script>
