import { createSandbox } from '@cseed/public-utils'
function descRender (h, { item, value }) {
  try {
    if (item.render) {
      return createSandbox.call(this, { code: item.render, h, item, value })
    }
  } catch (err) {
    console.warn(err)
    // this.$notify.error(this.$t('lower.ph71'))
  }
}

export default {
  options (h, conf, key) {
    const list = []
    conf.__slot__.options.forEach((desc, index) => {
      list.push(
        <el-descriptions-item
          span={desc.span}
          labelStyle={desc.labelStyle}
          contentStyle={desc.contentStyle}
        >
          <template slot="label">
            <i class={ desc.icon } style="margin-right: 5px"></i>
            { desc.label }
          </template>
          <span>
            { desc.render
              ? descRender.call(this, h, { item: desc, value: desc.value })
              : desc.value
            }
          </span>
        </el-descriptions-item>
      )
    })
    return list
  }
}
