import { createSandbox } from '@cseed/public-utils'
/**
 * 
 * @param {*} btns
 * @param {*} btn
 * @param {*} active
 * @returns
 */
export function isDisabled (currentIItem, btn, active) {
  const stepList = currentIItem.__slot__.options || []
  const len = stepList.length
  let flag = false
  if (btn.operate === 'prev' && active === 1) {
    flag = true
  } else if (btn.operate === 'next' && active === len) {
    flag = true
  }
  return flag
}

/**
 * renderMethods
 * @param {*} item
 * @param {*} btn
 */
export function operateClick (item, btn, form) {
  // eslint-disable-next-line @typescript-eslint/no-this-alias
  const _this = this
  let result
  if (btn.operate === 'next') {
    if (_this.$refs[this.formConf.formRef]) {
      _this.$refs[this.formConf.formRef].validate(valid => {
        if (valid) {
          item.active = ++item.active
        }
      })
    } else {
      item.active = ++item.active
    }
  } else if (btn.operate === 'prev') {
    item.active = --item.active
  } else {
    try {
      if (btn.render) {
        result = createSandbox.call(_this, { code: btn.render, data: form })
        result && this.goBack(result)
      }
    } catch (err) {
      console.warn(err)
      _this.$notify.error(_this.$t('lower.ph71'))
    }
  }
}
