export default {
  options (h, conf, key) {
    const list = []
    conf.__slot__.options.forEach(item => {
      if (conf.__config__.optionType === 'button') {
        list.push(
          <el-radio-button
            label={item.value}
            disabled={item.disabled}
          >{item.label}</el-radio-button>
        )
      } else {
        list.push(
          <el-radio
            class={conf.__config__.direction === 'vertical' ? 'customVertical' : '' }
            label={item.value}
            disabled={item.disabled}
          >{item.label}</el-radio>
        )
      }
    })
    return list
  }
}
