import { Notification } from 'element-ui'
export default {
  default (h, conf, key) {
    return h(
      'div',
      {
        domProps: {
          innerHTML: conf.__slot__[key]
        }
      },
      ''
    )
  },
  copyBtn (h, conf, key) {
    const value = conf.__config__.copyTxt
    return h(
      'el-button',
      {
        style: { 'margin-left': '10px' },
        props: {
          type: 'text',
          size: 'mini'
        },
        class: {
          [value]: value.indexOf('el-') > -1 ? true : ''
        },
        attrs: {
          title: ''
        },
        on: {
          click: () => {
            // input
            var _input = document.createElement('input')
            _input.setAttribute('readonly', 'readonly')
            _input.value = conf.__slot__.default
            document.body.appendChild(_input)
            _input.select()
            document.execCommand('copy')
            Notification.success('')
            document.body.removeChild(_input)
          }
        }
      },
      value.indexOf('el-') < 0 ? value : ''
    )
  }
}
