import cseedLocale from '@cseed/locale'
class BaseListFetchParamsError extends Error {
  constructor (msg) {
    super(msg)
    this.code = -1
    this.name = 'BaseListFetchParamsError'
  }
}
function baseListFetch ({ apiJson = {}, apiKey, data, params, isJSON }) {
  
  const apis = apiJson[apiKey]
  if (!apis) {
    return Promise.reject(new BaseListFetchParamsError(cseedLocale.$t('cMessage.missingApi', [apiKey])))
  }
  const [url, method, headers] = apis
  return this.fetchMethod({
    url, method, data, params, isJSON, headers
  })
}
export default { baseListFetch }
