<!--  -->
<template>
  <div class="filter-form">
    <c-form
      @submit.native.prevent
      @keyup.native.enter="handleFilter"
      :label-width="showLabel ? labelWidth : ''"
    >
      <div style="margin-bottom: 14px;line-height: 33px;">
        <slot name="prepend"></slot>
      </div>
      <template v-for="(item, key) in formItems">
        <template v-if="!item.queryExclude">
          <!--  -->
          <c-form-string
            v-if="!item.inputType || item.inputType === 'string'"
            :key="key"
            :label="showLabel ? $t(item.label) : ''"
            v-model="formData[item.prop]"
            clearable
            :placeholder="
              (item.placePrefix ? item.placePrefix : $t('cPlaceholder.pleaseEnters',[$t(item.label)])) 
            "
            @change="handleFilterChange"
          />
          <!--  -->
          <el-form-item
            v-if="item.inputType === 'number'"
            :key="key"
            :label="item.showLabel ? item.label : ''"
          >
            <el-input-number
              class="advance-inpt-number"
              :placeholder="`${$t('cPlaceholder.pleaseEnters',[$t(item.label)])}`"
              v-model="formData[item.prop]"
              controls-position="right"
              clearable
              @change="handleFilterChange"
            />
          </el-form-item>
          <!--  -->
          <c-form-enum
            v-if="item.inputType === 'enum'"
            :key="key"
            :label="showLabel ? $t(item.label) : ''"
            v-model="formData[item.prop]"
            :prop="item.prop"
            :options="item.options"
            :placeholder="`${$t('cPlaceholder.pleaseSelects',[$t(item.label)])}`"
            filterable
            clearable
            @change="handleFilterChange"
          />
          <!--  -->
          <el-cascader
            v-if="item.inputType === 'cascader'"
            :key="key"
            clearable
            :options="item.options"
            v-model="formatForm[item.prop]"
            :placeholder="$t(item.label)"
            :show-all-levels="false"
            @change="changeCascader(item.prop)"
            :props="{
              label: item.optionsApi.label || 'label',
              value: item.optionsApi.value || 'value',
              children: item.optionsApi.children || 'children',
              checkStrictly: true
            }"
          />
          <!--  -->
          <c-form-any
            v-if="item.inputType === 'date'"
            :prop="item.prop"
            :key="key"
            :label="showLabel ? $t(item.label) : ''"
          >
            <div slot="edit">
              <el-date-picker
                v-if="item.dateProp && item.dateProp.isRange"
                v-model="formatForm[item.prop]"
                @change="changeDateRange(item.prop, item.dateProp)"
                type="daterange"
                range-separator="-"
                :start-placeholder="$t('cMessage.startTime')"
                :end-placeholder="$t('cMessage.endTime')"
                :value-format="item.format || 'yyyy/MM/dd HH:mm:ss'"
              >
              </el-date-picker>
              <el-date-picker
                v-else
                v-model="formData[item.prop]"
                type="date"
                :placeholder="$t(item.label)"
                @change="handleFilterChange"
              >
              </el-date-picker>
            </div>
          </c-form-any>
          <slot name="loop" :data="item"></slot>
        </template>
      </template>
      <slot name="outloop"></slot>
      <div style="margin-left: 0; margin-bottom: 14px">
        <el-button-group v-if="isButton">
          <el-button class="text-btn" @click="handleFilter">
            {{ genButtonText.query }}
          </el-button>
          <el-button class="text-btn" @click="handleFilterReset" v-if="!hiddenResetButton">
            {{ genButtonText.reset }}
          </el-button>
          <template v-if="!hiddenExpandButton">
            <el-button
              v-if="!expand"
              class="text-btn"
              @click="handleFilterExpand"
            >
              {{ genButtonText.expand }} <i class="el-icon-arrow-down"></i>
            </el-button>
            <el-button
              v-if="expand"
              class="text-btn"
              @click="handleFilterExpand"
            >
              {{ genButtonText.packUp }} <i class="el-icon-arrow-up"></i>
            </el-button>
          </template>
        </el-button-group>
        <template v-else>
          <el-button type="text" class="text-btn" @click="handleFilter">
            {{ genButtonText.query }}
          </el-button>
          <el-divider direction="vertical"></el-divider>
          <el-button type="text" class="text-btn" @click="handleFilterReset">
            {{ genButtonText.reset }}
          </el-button>
          <template v-if="!hiddenExpandButton">
            <el-divider direction="vertical"></el-divider>
            <el-button
              v-if="!expand"
              type="text"
              class="text-btn"
              @click="handleFilterExpand"
            >
              {{ genButtonText.expand }} <i class="el-icon-arrow-down"></i>
            </el-button>
            <el-button
              v-if="expand"
              type="text"
              class="text-btn"
              @click="handleFilterExpand"
            >
              {{ genButtonText.packUp }} <i class="el-icon-arrow-up"></i>
            </el-button>
          </template>
        </template>
      </div>
      <div style="margin-bottom: 14px;line-height: 33px;"><slot name="append"></slot></div>
    </c-form>
  </div>
</template>

<script>
import CForm from '../../form/src/Form.vue'
import CFormString from '../../string/src/String.vue'
import CFormEnum from '../../enum/src/Enum.vue'
import CFormAny from '../../any/src/Any.vue'
export default {
  name: 'CListFilter',
  components: { CForm, CFormString, CFormEnum, CFormAny },
  data () {
    return {
      filter: {},
      expand: '',
      formatForm: {}
    }
  },
  props: {
    options: {
      type: Array,
      default: () => {
        return []
      }
    },
    formData: {
      type: Object
    },
    isExpand: {
      type: Boolean,
      default: false
    },
    length: {
      type: Number
    },
    hiddenExpandButton: {
      type: Boolean
    },
    hiddenResetButton: {
      type: Boolean
    },
    isButton: {
      type: Boolean,
      default: true
    },
    showLabel: {
      type: Boolean,
      default: false
    },
    labelWidth: {
      type: String,
      default: '80px'
    },
    buttonText: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  computed: {
    formItems () {
      // console.log(this.options, this.length)
      if (this.length) {
        return this.options.slice(0, this.length)
      } else {
        return this.options
      }
    },
    genButtonText () {
      return {
        query: this.$t('cButton.query'),
        reset: this.$t('cButton.reset'),
        expand: this.$t('cButton.expand'),
        packUp: this.$t('cButton.packUp'),
        ...this.buttonText
      }
    }
  },
  methods: {
    handleFilterChange(){
      this.$emit('filter-change', this.formData)
    },
    changeDateRange (name, item) {
      const arr = JSON.parse(JSON.stringify(this.formatForm[name]))
      this.$set(this.formData, item.startKey || 'startDate', arr && arr.length === 2 ? arr[0] : '')
      this.$set(this.formData, item.endKey || 'endDate', arr && arr.length === 2 ? arr[1] : '')
      this.handleFilterChange()
    },
    changeCascader (name) {
      const arr = JSON.parse(JSON.stringify(this.formatForm[name]))
      if (arr) {
        this.$set(this.formData, name, arr.pop())
      }
      this.handleFilterChange()
    },
    treeHandle (data) {
      return data
        ? data.map((item) => {
          if (item.children.length) {
            this.$set(item, 'children', this.treeHandle(item.children))
          } else {
            this.$delete(item, 'children')
          }
          return item
        })
        : []
    },
    handleFilter () {
      this.$emit('filter')
    },
    handleFilterReset () {
      this.formatForm = {}
      this.$emit('reset')
    },
    handleFilterExpand () {
      this.expand = !this.expand
      this.$emit('filter-expand', this.expand)
    }
  },
  created () {
    this.expand = this.isExpand
  }
}
</script>
<style lang="stylus" scoped>
>>>.el-form
  display flex
  flex-wrap wrap
  flex-direction row
  .el-form-item,.el-cascader
    min-width 220px
    margin-right 10px
    margin-bottom 14px !important
    align-self flex-start
.filter-form
  .advance-inpt-number
    width: 220px
    >>>.el-input__inner
      text-align: left
</style>